<template>

    <div> 
      <base-header type="gradient-success" class="pb-6 pb-8 pt-5">
            <!-- Card stats -->
      </base-header>     
      <div class="col-12 container-fluid mt--7" >
        <card>
        <div class="card-header bg-transparent pb-5">
          <div class="text-left">
            <h4>Bank Cheque List</h4>
          </div>       
          <div class="text-right">
              <base-button 
                @click.native="chequeSeries" outline type="success">
                Cheque Series
              </base-button>
              <base-button 
                @click.native="newCheque" outline type="primary">
                New Cheque
              </base-button>
          </div> 
        </div>

          <div class="input-group table-search">
            <input type="text" v-model="searchTerm" class="form-control" @input="resetData" v-on:keyup.enter="search" name="search" :placeholder="'Search Cheque Number.....'"> 
            <span v-if="searching" class="input-group-append" @click="reloadData">
              <i class="input-group-text ti-close text-danger"></i>
            </span>
            <span v-else class="input-group-append" @click="search">
              <base-button type="primary" icon="fa fa-search"></base-button>
            </span>
          </div>
          <div class="table-full-width">
             <paper-table 
               type="hover"
              :loading="loading"
              :loadError="loadError"
              @reloadData="reloadData"
              :title="bankChequeTable.title"
              :placeholder="'Search Bank Cheque Number..'"
              :sub-title="bankChequeTable.subTitle"
              :data="bankCheques"
              :columns="bankChequeTable.columns"
              :search="true">
                <template slot-scope="{ row }">
                  <td>{{row.rowNum}}</td>
                  <td>
                    <router-link :to='"/view/cheque/" + row.bank_cheque_uid'>{{ row.bank_cheque_number }}</router-link>
                  </td>
                  <td>{{ row.bank_cheque_amount }}</td>
                  <td>
                    <span v-for="bank in banks" v-bind:key="bank.bankUid" >
                      <span v-if="bank.bankUid == row.bank_reference"> {{ bank.bank }}</span>
                    </span>
                  </td>
                  <td>
                    <span v-for="bankAccount in bankAccounts" v-bind:key="bankAccount.bankAccountUid" >
                      <span v-if="bankAccount.bankAccountUid == row.bank_account_reference"> {{ bankAccount.bankAccount }}</span>
                    </span>
                  </td>
                  <td>{{ row.bank_cheque_type }}</td>
                  <td align="right">{{ row.cheque_status }}</td>
                </template>
            </paper-table>
          </div>

          <hr>
          <div class="row" v-if="!loading && bankCheques.length">
            <div class="col-8">
              <span class="pagination-results">{{bankCheques[bankCheques.length - 1].rowNum}} of {{bankChequesCount}}</span>
            </div>
            <pagination
              :searching="searching"
              :searchTotalPage="bankCheques"
              :totalPages="bankChequesCount"
              :currentPage="currentPage"
              @pagechanged="onPageChange">
            </pagination>
          </div>
        </card>
      </div>
    </div>
</template>
<script>
import { mapGetters } from 'vuex';
import { api } from '@/main';
// import { helpers, required } from "vuelidate/lib/validators";
// const alphaNumCustomValidator = helpers.regex('alphaNumAndDot', /^[ A-Za-z0-9. ]*$/);
const tableColumns = ["#", "Cheque Number", "Amount", "Bank", "Bank Account", "Cheque For", "Status"];

export default {
  components: {
    // PaperTable,
  },
   data() {
    return {
      loading: true,
      loadError: false,
      bankChequeTable: {
        title: "Bank Cheques Table",
        subTitle: "",
        columns: [...tableColumns],
      },

      bankChequesCount: 0,
      bankCheques: [],
      banks: [],
      activateBanks: [],
      bankAccounts: [],
      activateBankAccounts: [],

      currentPage: 1 ,
      searchTerm: '',
      searching: false ,

      requestProcessing: false,
    };
  },

  computed: {
    ...mapGetters({
      // permission: "Settings/permission" ,
    })
  },
  async mounted(){
    this.getPaginatedBankCheques(1, "");
    this.getBanks();
    this.getBankAccounts();
    this.checkToken();
  },
  methods: {
    checkToken(){
      if(!localStorage.getItem('aistoken')){
        this.$router.push("/login");
      }
    },
    /* paginate */
    getPaginatedBankCheques(page, searchTerm) {
      let self = this;
      self.loading = true;
      self.loadError = false;
      self.bankCheques = [];

      if(searchTerm) {
        self.searching = true;
      }

      fetch(api + '/banks/cheque/paginated/get/' + page + '/' + searchTerm).then(function(response) {
        return response.json();
      }).then(function(response) {
        if(response.success) {
          let rowNum = (10 * page) - 9;
          for(var i = 0; i < response.output.bankCheques.length; i++){
            response.output.bankCheques[i]["rowNum"] = rowNum + i;
          }

          self.currentPage = page;
          self.bankCheques = response.output.bankCheques;
          self.bankChequesCount = response.output.count;
        }
      }).catch(function() {
        self.loadError = true;
      }).then(function() {
        setTimeout(function() {
          self.loading = false;
        }, 1500);            
      });
    },

    onPageChange (page) {
      let searchTerm = this.searchTerm;

      this.getPaginatedBankCheques(page, searchTerm);
    } ,

    resetData() {
      this.searching = false;
        if(this.searchTerm == "") {
          this.getPaginatedBankCheques(1, "");
      }
    },

    reloadData() {
      this.searchTerm = "";
      this.searching = false;
      this.getPaginatedBankCheques(1, "");
    },

    /* Search */
    search() {
      let searchTerm = this.searchTerm;
      if(searchTerm) {
        this.getPaginatedBankCheques(1, searchTerm);
      }
    } ,

    getBanks(){
      let self = this;
      self.banks = [];

      fetch(api + '/banks/bank/get/all').then(function(response) {
        return response.json();
      }).then(function(response) {
        if(response.success) {
          self.banks = response.output.banks;
          self.newBank = response.output.banks[0].bankUid;
        }
      }).catch(function(error) {
        console.log(error);
        self.loadError = true;
      });
    },

    getBankAccounts(){
      let self = this;
      self.bankAccounts = [];

      fetch(api + '/banks/account/get/all').then(function(response) {
        return response.json();
      }).then(function(response) {
        if(response.success) {
          self.bankAccounts = response.output.bankAccounts;
        }
      }).catch(function(error) {
        console.log(error);
        self.loadError = true;
      });
    },

    chequeSeries(){
      this.$router.push("/cheque/series");
    },

    newCheque(){
      this.$router.push("/new/cheque");
    },

    updateCheque(id){
      this.$router.push("/update/cheque/" + id);
    },
  }
};
</script>