<template>

    <div> 
      <base-header type="gradient-success" class="pb-6 pb-8 pt-5">
            <!-- Card stats -->
      </base-header>     
      <div class="col-12 container-fluid mt--7" >
        <card>
        <div class="card-header bg-transparent pb-5">
          <div class="text-left">
            <h4>Bank Account List</h4>
          </div>       
          <div class="text-right">
              <base-button 
                v-b-modal.new-bank-account-modal outline type="primary">
                New Bank Account
              </base-button>
          </div> 
        </div>

          <div class="input-group table-search">
            <input type="text" v-model="searchTerm" class="form-control" @input="resetData" v-on:keyup.enter="search" name="search" :placeholder="'Search Bank Account.....'"> 
            <span v-if="searching" class="input-group-append" @click="reloadData">
              <i class="input-group-text ti-close text-danger"></i>
            </span>
            <span v-else class="input-group-append" @click="search">
              <base-button type="primary" icon="fa fa-search"></base-button>
            </span>
          </div>
          <div class="table-full-width">
             <paper-table 
               type="hover"
              :loading="loading"
              :loadError="loadError"
              @reloadData="reloadData"
              :title="bankAccountTable.title"
              :placeholder="'Search Bank..'"
              :sub-title="bankAccountTable.subTitle"
              :data="bankAccounts"
              :columns="bankAccountTable.columns"
              :search="true">
                <template slot-scope="{ row }">
                  <td> {{row.rowNum}}</td>
                  <td>{{ row.bank_account }}</td>
                  <td>
                    <span v-for="bank in banks" v-bind:key="bank.bankUid" >
                      <span v-if="bank.bankUid == row.bank_reference"> {{ bank.bank }}</span>
                    </span>
                  </td>
                  <td >{{ row.date_created | moment("MMM DD, YYYY") }}</td>
                  <td>
                    {{ row.status == 1 ? 'Active' : 'Disabled'}}
                  </td>
                  <td align="right" v-if="row.status == 1">
                    <span class="pointer ml-2 far fa-edit text-blue" v-on:click="openUpdateBankAccountModal(row)" title="Update"></span>
                    <span class="pointer ml-2 far fa-times-circle text-red" v-on:click="deactivateBankAccount(row)" title="Disable"></span>
                  </td>
                  <td align="right" v-else>
                    <span class="pointer ml-2 far fa-check-circle text-green" v-on:click="activateBankAccount(row)" title="Activate"></span>
                  </td>
                </template>
            </paper-table>
          </div>

          <hr>
          <div class="row" v-if="!loading && bankAccounts.length">
            <div class="col-8">
              <span class="pagination-results">{{bankAccounts[bankAccounts.length - 1].rowNum}} of {{bankAccountsCount}}</span>
            </div>
            <pagination
              :searching="searching"
              :searchTotalPage="bankAccounts"
              :totalPages="bankAccountsCount"
              :currentPage="currentPage"
              @pagechanged="onPageChange">
            </pagination>
          </div>
        </card>

        <!-- ADD BANK ACCOUNT MODAL -->
        <b-modal id="new-bank-account-modal" title="New Bank Account" centered :hide-header-close="true" :no-close-on-backdrop="true" :no-close-on-esc="true">

          <div class="form-group row">
            <label for="bankAccount" class="col-sm-12 col-form-label">Bank Account</label>
            <div class="col-sm-12">
              <base-input v-model="newBankAccount" id="bankAccount" type="text" class="form-control-plaintext" placeholder="Bank Account"></base-input>
                <i class="text-danger" v-if="$v.newBankAccount.$anyDirty && !$v.newBankAccount.required">Field Required</i>
                <i class="text-danger" v-if="$v.newBankAccount.$anyDirty && !$v.newBankAccount.alphaNumCustomValidator">Invalid Characters</i>
            </div>
            <label for="bankAccountDate" class="col-sm-12 col-form-label">Bank Account Created</label>
            <div class="col-sm-12">
              <base-input v-model="newBankAccountCreated" id="bankAccountDate" type="date" class="form-control-plaintext" placeholder="Bank Account Created"></base-input>
                <i class="text-danger" v-if="$v.newBankAccountCreated.$anyDirty && !$v.newBankAccountCreated.required">Field Required</i>
            </div>
            <label for="bank" class="col-sm-12 col-form-label">Bank</label>
            <div class="col-sm-12">
              <v-select class="form-control-plaintext" :options="activateBanks" :reduce="activateBank => activateBank.bankUid" label="bank" v-model="newBank">
              </v-select>
              <i class="text-danger" v-if="$v.newBank.$anyDirty && !$v.newBank.required">Field Required</i>
            </div>
          </div>

          <template v-slot:modal-footer>
             <div class="w-100">
                <div class="pull-right">
                   <base-button type="primary" @click.native="addBankAccount" :disabled="requestProcessing">
                      <i v-if="requestProcessing" class="fa fa-spinner fa-spin"></i>
                      Add
                   </base-button>
                   <base-button type="danger" @click.native="$bvModal.hide('new-bank-account-modal')" :disabled="requestProcessing">Cancel</base-button>
                </div>
            </div>
          </template>
        </b-modal>

        <!-- UPDATE BANK ACCOUNT MODAL -->
        <b-modal id="update-bank-account-modal" title="Update Bank Account" centered :hide-header-close="true" :no-close-on-backdrop="true" :no-close-on-esc="true">

            <div class="form-group row">
               <label for="bankAccount" class="col-sm-12 col-form-label">Bank Account</label>
               <div class="col-sm-12">
                  <base-input v-model="selectedBankAccount.bankAccount" type="text" class="form-control-plaintext" placeholder="Bank Account"></base-input>
                  <i class="text-danger" v-if="$v.selectedBankAccount.$anyDirty && !$v.selectedBankAccount.bankAccount.required">Field Required</i>
                  <i class="text-danger" v-if="$v.selectedBankAccount.$anyDirty && !$v.selectedBankAccount.bankAccount.alphaNumCustomValidator">Invalid Characters</i>
               </div>
              <label for="bankAccountDate" class="col-sm-12 col-form-label">Bank Account Created</label>
              <div class="col-sm-12">
                <base-input v-model="selectedBankAccount.bankAccountCreated" id="bankAccountDate" type="date" class="form-control-plaintext" placeholder="Bank Account Created"></base-input>
                <i class="text-danger" v-if="$v.selectedBankAccount.$anyDirty && !$v.selectedBankAccount.bankAccountCreated.required">Field Required</i>
              </div>
              <label class="col-sm-12 col-form-label">Bank</label>
              <div class="col-sm-12">
                <v-select class="form-control-plaintext" :options="activateBanks" :reduce="activateBank => activateBank.bankUid" label="bank" v-model="selectedBankAccount.bank">
                </v-select>
                <i class="text-danger" v-if="$v.selectedBankAccount.bank.$anyDirty && !$v.selectedBankAccount.bank.required">Field Required</i>
              </div>
            </div>

            <template v-slot:modal-footer>
               <div class="w-100">
                  <div class="pull-right">
                     <base-button type="primary" @click.native="updateBankAccount" :disabled="requestProcessing">
                        <i v-if="requestProcessing" class="fa fa-spinner fa-spin"></i>
                        Update
                     </base-button>
                     <base-button type="danger" @click.native="$bvModal.hide('update-bank-account-modal')" :disabled="requestProcessing">Cancel</base-button>
                  </div>
              </div>
            </template>
          </b-modal>

      </div>
    </div>
</template>
<script>
import { mapGetters } from 'vuex';
import { api } from '@/main';
import { helpers, required } from "vuelidate/lib/validators";
const alphaNumCustomValidator = helpers.regex('alphaNumAndDot', /^[ A-Za-z0-9. ]*$/);
const tableColumns = ["#", "Bank Account", "Bank", "Date Added", "Status", "Actions"];

export default {
  components: {
    // PaperTable,
  },
   data() {
    return {
      loading: true,
      loadError: false,
      bankAccountTable: {
        title: "Bank Accounts Table",
        subTitle: "",
        columns: [...tableColumns],
      },

      bankAccountsCount: 0,
      bankAccounts: [],
      banks: [],
      activateBanks: [],

      currentPage: 1 ,
      searchTerm: '',
      searching: false ,

      newBankAccount: '',
      newBankAccountCreated: '',
      newBank: '',
      selectedBankAccount: {
        bankUid: null,
        bankAccount: '',
        bankAccountCreated: '',
        bank: ''
      },

      bankUid: '',

      requestProcessing: false,
    };
  },

  computed: {
    ...mapGetters({
      // permission: "Settings/permission" ,
    })
  },
  validations: {
    newBankAccount: { required, alphaNumCustomValidator },
    newBankAccountCreated: { required },
    newBank: { required },
    selectedBankAccount: {
      bankAccount: { required, alphaNumCustomValidator },
      bankAccountCreated: { required },
      bank: { required }
    },
  },
  async mounted(){
    this.getPaginatedBankAccounts(1, "");
    this.getBanks();
    this.getActivateBanks();this.checkToken();
  },
  methods: {
    checkToken(){
      if(!localStorage.getItem('aistoken')){
        this.$router.push("/login");
      }
    },
    /* paginate */
    getPaginatedBankAccounts(page, searchTerm) {
      let self = this;
      self.loading = true;
      self.loadError = false;
      self.bankAccounts = [];

      if(searchTerm) {
        self.searching = true;
      }

      fetch(api + '/banks/account/paginated/get/' + page + '/' + searchTerm).then(function(response) {
        return response.json();
      }).then(function(response) {
        if(response.success) {
          let rowNum = (10 * page) - 9;
          for(var i = 0; i < response.output.bankAccounts.length; i++){
            response.output.bankAccounts[i]["rowNum"] = rowNum + i;
          }

          self.currentPage = page;
          self.bankAccounts = response.output.bankAccounts;
          self.bankAccountsCount = response.output.count;
        }
      }).catch(function() {
        self.loadError = true;
      }).then(function() {
        setTimeout(function() {
          self.loading = false;
        }, 1500);            
      });
    },

    onPageChange (page) {
      let searchTerm = this.searchTerm;

      this.getPaginatedBankAccounts(page, searchTerm);
    } ,

    resetData() {
      this.searching = false;
        if(this.searchTerm == "") {
          this.getPaginatedBankAccounts(1, "");
      }
    },

    reloadData() {
      this.searchTerm = "";
      this.searching = false;
      this.getPaginatedBankAccounts(1, "");
    },

    /* Search */
    search() {
      let searchTerm = this.searchTerm;
      if(searchTerm) {
        this.getPaginatedBankAccounts(1, searchTerm);
      }
    } ,

    getBanks(){
      let self = this;
      self.banks = [];

      fetch(api + '/banks/bank/get/all').then(function(response) {
        return response.json();
      }).then(function(response) {
        if(response.success) {
          self.banks = response.output.banks;
          self.newBank = response.output.banks[0].bankUid;
        }
      }).catch(function(error) {
        console.log(error);
        self.loadError = true;
      });
    },

    getActivateBanks(){
      let self = this;
      self.activateBanks = [];

      fetch(api + '/banks/bank/get/activate').then(function(response) {
        return response.json();
      }).then(function(response) {
        if(response.success) {
          self.activateBanks = response.output.banks;
          self.newBank = response.output.banks[0].bankUid;
        }
      }).catch(function(error) {
        console.log(error);
        self.loadError = true;
      });
    },

    addBankAccount(){
      let self = this;   
      self.requestProcessing = false;

      self.$v.newBankAccount.$touch();
      self.$v.newBankAccountCreated.$touch();
      self.$v.newBank.$touch();

      if(!self.$v.newBankAccount.$error && !self.$v.newBankAccountCreated.$error && !self.$v.newBank.$error){

        self.requestProcessing = true;
        let username = localStorage.getItem('aisname');

        let newBankAccount = {
          bankAccount: self.newBankAccount,
          bankAccountDate: self.newBankAccountCreated,
          bank: self.newBank,
          createdBy: username
        }
        fetch(api + '/banks/account/new/',{
            method: 'post',
            body: JSON.stringify(newBankAccount),
            headers: {
              'Content-Type': 'application/json'
            }
        }).then( response => {
          return response.json();
        }).then(data => {
          if(data.success) {
            self.newBankAccount = '';
            self.newBankAccountCreated = '';
            self.$v.newBankAccount.$reset();
            self.$v.newBankAccountCreated.$reset();
            self.$swal({
              title: 'Success',
              text: 'New Bank Account added successfully!',
              icon: 'success' ,
              confirmButtonColor: '#51cbce',
              confirmButtonText: 'OK'
            }).then(() => {
              self.$bvModal.hide('new-bank-account-modal');
              self.getActivateBanks();
            });
          } else {
            self.requestProcessing = false;
            self.$swal({
               title: 'Error',
               text: data.errorMessage,
               icon: 'error' ,
               confirmButtonColor: '#ef8157',
               confirmButtonText: 'OK'
            });
          }
        }).catch(function() {

        }).then(function() {
          self.requestProcessing = false;
          self.getPaginatedBankAccounts(1, "");
        });
      }
    },

    openUpdateBankAccountModal(account){
      this.selectedBankAccount = {
        bankAccountUid: account.bank_account_uid,
        bankAccount: account.bank_account,
        bankAccountCreated: account.bank_account_date,
        bank: account.bank_reference
      }
      this.$bvModal.show('update-bank-account-modal')
    },

    updateBankAccount(){
      let self = this;  
      self.requestProcessing = true;

      self.$v.selectedBankAccount.$touch();

      if(!self.$v.selectedBankAccount.$error){
        let updateBankAccount = {
          bankAccountUid: self.selectedBankAccount.bankAccountUid,
          bankAccount: self.selectedBankAccount.bankAccount,
          bankAccountCreated: self.selectedBankAccount.bankAccountCreated,
          bank: self.selectedBankAccount.bank
        }
        
        fetch(api + '/banks/account/update/', {
          method: 'post',
          body: JSON.stringify(updateBankAccount),
          headers: {
            'Content-Type': 'application/json'
          }
        }).then( response => {
           return response.json();
        }).then(data => {
           if(data.success) {
              self.$swal({
                 title: 'Success',
                 text: 'Bank Account updated successfully!',
                 icon: 'success' ,
                 confirmButtonColor: '#51cbce',
                 confirmButtonText: 'OK'
              }).then(() => {
                 self.selectedBankAccount = {};
                 self.$v.selectedBankAccount.$reset();
                 self.$bvModal.hide('update-bank-account-modal');
              });
           } else {
            self.requestProcessing = false;
              self.$swal({
                 title: 'Error',
                 text: data.errorMessage,
                 icon: 'error' ,
                 confirmButtonColor: '#ef8157',
                 confirmButtonText: 'OK'
              });
           }
        }).catch(function(error) {
           console.log(error);
        }).then(function() {
           self.requestProcessing = false;
           self.getPaginatedBankAccounts(1, "");
        });
      }
    },

    deactivateBankAccount(account){
      let self = this;
      let deactivateBankAccount = {
        status: 0,
        bankAccountUid: account.bank_account_uid,
        bank: account.bank_reference
      } 
      
      this.$swal({
          title: 'Are you sure?',
          text: "You want to disable this Bank Account",
          icon: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#51cbce',
          cancelButtonColor: '#ef8157',
          confirmButtonText: 'CONFIRM' ,
          cancelButtonText: 'CANCEL' ,
      }).then((result) => {
        if (result.value) {
          fetch(api + '/banks/account/status/update',{
            method: 'post',
            body: JSON.stringify(deactivateBankAccount),
            headers: {
              'Content-Type': 'application/json'
            }
          }).then( response => {
            return response.json();
          }).then(data => {
            if(data.success) {
              self.$swal({
                title: 'Success',
                text: 'Bank Account disabled!',
                icon: 'success' ,
                confirmButtonColor: '#51cbce',
                confirmButtonText: 'OK'
              }).then(() => {
                self.updateBankAccountModal = false;
              });
            } else {
              self.$swal({
                title: 'Error',
                text: data.errorMessage,
                icon: 'error',
                confirmButtonColor: '#ef8157',
                confirmButtonText: 'OK'
              });
            }
          }).catch(function() {

          }).then(function() {
            self.getPaginatedBankAccounts(1, "");
          });
        }
      }); 
    },

    activateBankAccount(account){
      let self = this;
      let activateBankAccount = {
        status: 1,
        bankAccountUid: account.bank_account_uid
      } 
      
      this.$swal({
          title: 'Are you sure?',
          text: "You want to activate this Bank Account",
          icon: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#51cbce',
          cancelButtonColor: '#ef8157',
          confirmButtonText: 'CONFIRM' ,
          cancelButtonText: 'CANCEL' ,
      }).then((result) => {
        if (result.value) {
          fetch(api + '/banks/account/status/update',{
            method: 'post',
            body: JSON.stringify(activateBankAccount),
            headers: {
              'Content-Type': 'application/json'
            }
          }).then( response => {
            return response.json();
          }).then(data => {
            if(data.success) {
              self.$swal({
                title: 'Success',
                text: 'Bank Account activate!',
                icon: 'success' ,
                confirmButtonColor: '#51cbce',
                confirmButtonText: 'OK'
              }).then(() => {
                self.updateBankAccountModal = false;
              });
            } else {
              self.$swal({
                title: 'Error',
                text: data.errorMessage,
                icon: 'error',
                confirmButtonColor: '#ef8157',
                confirmButtonText: 'OK'
              });
            }
              console.log(data);
          }).catch(function() {

          }).then(function() {
            self.getPaginatedBankAccounts(1, "");
          });
        }
      });
    },
  }
};
</script>