<template>

    <div> 
      <base-header type="gradient-success" class="pb-6 pb-8 pt-5">
            <!-- Card stats -->
      </base-header>     
      <div class="col-12 container-fluid mt--7" >
        <card>
        <div class="card-header bg-transparent pb-5">
          <div class="text-left">
            <h4>Cheque Series List</h4>
          </div>       
          <div class="text-right">
              <base-button 
                @click.native="chequeLists" outline type="success">
                Cheque List
              </base-button>
              <base-button 
                v-b-modal.new-cheque-series-modal outline type="primary">
                New Cheque Series
              </base-button>
          </div> 
        </div>

          <div class="input-group table-search">
            <input type="text" v-model="searchTerm" class="form-control" @input="resetData" v-on:keyup.enter="search" name="search" :placeholder="'Search Series Number.....'"> 
            <span v-if="searching" class="input-group-append" @click="reloadData">
              <i class="input-group-text ti-close text-danger"></i>
            </span>
            <span v-else class="input-group-append" @click="search">
              <base-button type="primary" icon="fa fa-search"></base-button>
            </span>
          </div>
          <div class="table-full-width">
             <paper-table 
               type="hover"
              :loading="loading"
              :loadError="loadError"
              @reloadData="reloadData"
              :title="chequeSeriesTable.title"
              :placeholder="'Search Cheque Series..'"
              :sub-title="chequeSeriesTable.subTitle"
              :data="chequeSeries"
              :columns="chequeSeriesTable.columns"
              :search="true">
                <template slot-scope="{ row }">
                  <td>{{row.rowNum}}</td>
                  <td>{{ row.bank_cheque_series }}</td>
                  <td>
                    <span v-for="account in bankAccounts" v-bind:key="account.bankAccountUid" >
                      <span v-if="account.bankAccountUid == row.bank_account_reference"> {{ account.bankAccount }}</span>
                    </span>
                  </td>
                  <td >{{ row.bank_cheque_series_date | moment("MMM DD, YYYY") }}</td>
                  <td>
                    <span v-for="bank in banks" v-bind:key="bank.bankUid" >
                      <span v-if="bank.bankUid == row.bank_reference"> {{ bank.bank }}</span>
                    </span>
                  </td>
                  <td >{{ row.date_created | moment("MMM DD, YYYY") }}</td>
                  <td align="right">
                    <span class="pointer ml-2 far fa-edit text-blue" v-on:click="openUpdateChequeSeriesModal(row)" title="Update"></span>
                  </td>
                </template>
            </paper-table>
          </div>

          <hr>
          <div class="row" v-if="!loading && chequeSeries.length">
            <div class="col-8">
              <span class="pagination-results">{{chequeSeries[chequeSeries.length - 1].rowNum}} of {{chequeSeriesCount}}</span>
            </div>
            <pagination
              :searching="searching"
              :searchTotalPage="chequeSeries"
              :totalPages="chequeSeriesCount"
              :currentPage="currentPage"
              @pagechanged="onPageChange">
            </pagination>
          </div>
        </card>

        <!-- ADD CHEQUE SERIES MODAL -->
        <b-modal id="new-cheque-series-modal" title="New Cheque Series" centered :hide-header-close="true" :no-close-on-backdrop="true" :no-close-on-esc="true">

          <div class="form-group row">
            <label for="seriesNumber" class="col-sm-12 col-form-label">Series Number</label>
            <div class="col-sm-12">
              <base-input v-model="newSeriesNumber" id="bankAccount" type="text" class="form-control-plaintext" placeholder="Series Number"></base-input>
                <i class="text-danger" v-if="$v.newSeriesNumber.$anyDirty && !$v.newSeriesNumber.required">Field Required</i>
                <i class="text-danger" v-if="$v.newSeriesNumber.$anyDirty && !$v.newSeriesNumber.numCustomValidator">Invalid Characters</i>
            </div>
            <label for="seriesDate" class="col-sm-12 col-form-label">Series Date</label>
            <div class="col-sm-12">
              <base-input v-model="newSeriesDate" id="seriesDate" type="date" class="form-control-plaintext" placeholder="Series Date"></base-input>
                <i class="text-danger" v-if="$v.newSeriesDate.$anyDirty && !$v.newSeriesDate.required">Field Required</i>
            </div>
            <label for="bank" class="col-sm-12 col-form-label">Bank</label>
            <div class="col-sm-12">
              <v-select class="form-control-plaintext" :options="activateBanks" :reduce="activateBank => activateBank.bankUid" label="bank" v-model="newBank" @input="getActivateBankAccounts(newBank)">
              </v-select>
              <i class="text-danger" v-if="$v.newBank.$anyDirty && !$v.newBank.required">Field Required</i>
            </div>
            <label for="bankAccount" class="col-sm-12 col-form-label">Bank Account</label>
            <div class="col-sm-12" v-if="activateBankAccounts == ''">
              <span class="text-danger"> No Bank Account </span>
            </div>
            <div class="col-sm-12" v-else>
              <v-select class="form-control-plaintext" :options="activateBankAccounts" :reduce="activateBankAccount => activateBankAccount.bankAccountUid" label="bankAccount" v-model="newBankAccount">
              </v-select>
              <i class="text-danger" v-if="$v.newBankAccount.$anyDirty && !$v.newBankAccount.required">Field Required</i>
            </div>
          </div>

          <template v-slot:modal-footer>
             <div class="w-100">
                <div class="pull-right">
                   <base-button type="primary" @click.native="addChequeSeries" :disabled="requestProcessing" v-if="activateBankAccounts != ''">
                      <i v-if="requestProcessing" class="fa fa-spinner fa-spin"></i>
                      Add
                   </base-button>
                   <base-button type="danger" @click.native="$bvModal.hide('new-cheque-series-modal')" :disabled="requestProcessing">Cancel</base-button>
                </div>
            </div>
          </template>
        </b-modal>

        <!-- UPDATE CHEQUE SERIES MODAL -->
         <b-modal id="update-cheque-series-modal" title="Update Cheque Series" centered :hide-header-close="true" :no-close-on-backdrop="true" :no-close-on-esc="true">

            <div class="form-group row">
               <label for="seriesNumber" class="col-sm-12 col-form-label">Series Number</label>
               <div class="col-sm-12">
                  <base-input v-model="selectedChequeSeries.seriesNumber" type="text" class="form-control-plaintext" placeholder="Series Number"></base-input>
                  <i class="text-danger" v-if="$v.selectedChequeSeries.$anyDirty && !$v.selectedChequeSeries.seriesNumber.required">Field Required</i>
                  <i class="text-danger" v-if="$v.selectedChequeSeries.$anyDirty && !$v.selectedChequeSeries.seriesNumber.numCustomValidator">Invalid Characters</i>
              </div>
              <label for="seriesDate" class="col-sm-12 col-form-label">Series Date</label>
              <div class="col-sm-12">
                <base-input v-model="selectedChequeSeries.seriesDate" id="seriesDate" type="date" class="form-control-plaintext" placeholder="Series Date"></base-input>
                <i class="text-danger" v-if="$v.selectedChequeSeries.$anyDirty && !$v.selectedChequeSeries.seriesDate.required">Field Required</i>
              </div>
              <label class="col-sm-12 col-form-label">Bank</label>
              <div class="col-sm-12">
                <v-select class="form-control-plaintext" :options="activateBanks" :reduce="activateBank => activateBank.bankUid" label="bank" v-model="selectedChequeSeries.bank" @input="getActivateBankAccounts(selectedChequeSeries.bank)">
                </v-select>
                <i class="text-danger" v-if="$v.selectedChequeSeries.bank.$anyDirty && !$v.selectedChequeSeries.bank.required">Field Required</i>
              </div>
              <label for="bankAccount" class="col-sm-12 col-form-label">Bank Account</label>
              <div class="col-sm-12" v-if="activateBankAccounts == ''">
                <span class="text-danger"> No Bank Account </span>
              </div>
              <div class="col-sm-12" v-else>
                <v-select class="form-control-plaintext" :options="activateBankAccounts" :reduce="activateBankAccount => activateBankAccount.bankAccountUid" label="bankAccount" v-model="selectedChequeSeries.bankAccount">
                </v-select>
                <i class="text-danger" v-if="$v.selectedChequeSeries.$anyDirty && !$v.selectedChequeSeries.bankAccount.required">Field Required</i>
              </div>
            </div>

            <template v-slot:modal-footer>
               <div class="w-100">
                  <div class="pull-right">
                     <base-button type="primary" @click.native="updateChequeSeries" :disabled="requestProcessing" v-if="activateBankAccounts != ''">
                        <i v-if="requestProcessing" class="fa fa-spinner fa-spin"></i>
                        Update
                     </base-button>
                     <base-button type="danger" @click.native="$bvModal.hide('update-cheque-series-modal')" :disabled="requestProcessing">Cancel</base-button>
                  </div>
              </div>
            </template>
          </b-modal>

      </div>
    </div>
</template>
<script>
import { mapGetters } from 'vuex';
import { api } from '@/main';
import { helpers, required } from "vuelidate/lib/validators";
const numCustomValidator = helpers.regex('alphaNumAndDot', /^[ 0-9 ]*$/);
// const alphaNumCustomValidator = helpers.regex('alphaNumAndDot', /^[ A-Za-z0-9. ]*$/);
const tableColumns = ["#", "Series Number", "Bank Account", "Series Date", "Bank", "Date Added", "Actions"];

export default {
  components: {
    // PaperTable,
  },
   data() {
    return {
      loading: true,
      loadError: false,
      chequeSeriesTable: {
        title: "Cheque Series Table",
        subTitle: "",
        columns: [...tableColumns],
      },

      chequeSeriesCount: 0,
      chequeSeries: [],
      banks: [],
      activateBanks: [],
      bankAccounts: [],
      activateBankAccounts: [],

      currentPage: 1 ,
      searchTerm: '',
      searching: false ,

      newSeriesNumber: '',
      newSeriesDate: '',
      newBank: '',
      newBankAccount: '',
      selectedChequeSeries: {
        chequeSeriesUid: null,
        seriesNumber: '',
        seriesDate: '',
        bank: '',
        bankAccount: ''
      },

      // bankUid: '',

      requestProcessing: false,
    };
  },

  computed: {
    ...mapGetters({
      // permission: "Settings/permission" ,
    })
  },
  validations: {
    newSeriesNumber: { required, numCustomValidator },
    newSeriesDate: { required },
    newBank: { required },
    newBankAccount: { required },
    selectedChequeSeries: {
      seriesNumber: { required, numCustomValidator },
      seriesDate: { required },
      bank: { required },
      bankAccount: { required }
    },
  },
  async mounted(){
    this.getPaginatedChequeSeries(1, "");
    this.getBanks();
    this.getBankAccounts();
    this.getActivateBanks();
    this.checkToken();
  },
  methods: {
    checkToken(){
      if(!localStorage.getItem('aistoken')){
        this.$router.push("/login");
      }
    },
    /* paginate */
    getPaginatedChequeSeries(page, searchTerm) {
      let self = this;
      self.loading = true;
      self.loadError = false;
      self.chequeSeries = [];

      if(searchTerm) {
        self.searching = true;
      }

      fetch(api + '/banks/cheque/series/paginated/get/' + page + '/' + searchTerm).then(function(response) {
        return response.json();
      }).then(function(response) {
        if(response.success) {
          let rowNum = (10 * page) - 9;
          for(var i = 0; i < response.output.chequeSeries.length; i++){
            response.output.chequeSeries[i]["rowNum"] = rowNum + i;
          }

          self.currentPage = page;
          self.chequeSeries = response.output.chequeSeries;
          self.chequeSeriesCount = response.output.count;
        }
      }).catch(function() {
        self.loadError = true;
      }).then(function() {
        setTimeout(function() {
          self.loading = false;
        }, 1500);            
      });
    },

    onPageChange (page) {
      let searchTerm = this.searchTerm;

      this.getPaginatedChequeSeries(page, searchTerm);
    } ,

    resetData() {
      this.searching = false;
        if(this.searchTerm == "") {
          this.getPaginatedChequeSeries(1, "");
      }
    },

    reloadData() {
      this.searchTerm = "";
      this.searching = false;
      this.getPaginatedChequeSeries(1, "");
    },

    /* Search */
    search() {
      let searchTerm = this.searchTerm;
      if(searchTerm) {
        this.getPaginatedChequeSeries(1, searchTerm);
      }
    } ,

    getBanks(){
      let self = this;
      self.banks = [];

      fetch(api + '/banks/bank/get/all').then(function(response) {
        return response.json();
      }).then(function(response) {
        if(response.success) {
          self.banks = response.output.banks;
          self.newBank = response.output.banks[0].bankUid;
        }
      }).catch(function(error) {
        console.log(error);
        self.loadError = true;
      });
    },

    getBankAccounts(){
      let self = this;
      self.bankAccounts = [];

      fetch(api + '/banks/account/get/all/').then(function(response) {
        return response.json();
      }).then(function(response) {
        if(response.success) {
          self.bankAccounts = response.output.bankAccounts;
        }
      }).catch(function(error) {
        console.log(error);
        self.loadError = true;
      });
    },

    chequeLists(){
      this.$router.push("/bank/cheque");
    },

    getActivateBanks(){
      let self = this;
      self.activateBanks = [];

      fetch(api + '/banks/bank/get/activate').then(function(response) {
        return response.json();
      }).then(function(response) {
        if(response.success) {
          self.activateBanks = response.output.banks;
          self.newBank = response.output.banks[0].bankUid;
          self.getActivateBankAccounts(response.output.banks[0].bankUid);
        }
      }).catch(function(error) {
        console.log(error);
        self.loadError = true;
      });
    },

    getActivateBankAccounts(bank){
      let self = this;
      self.activateBankAccounts = [];
      
      fetch(api + '/banks/account/get/activate/' + bank).then(function(response) {
        return response.json();
      }).then(function(response) {
        if(response.success) {
          self.activateBankAccounts = response.output.bankAccounts;
          self.newBankAccount = response.output.bankAccounts[0].bankAccountUid;
          self.selectedChequeSeries.bankAccount = response.output.bankAccounts[0].bankAccountUid;
        }
      }).catch(function(error) {
        console.log(error);
        self.loadError = true;
      });
    },

    addChequeSeries(){
      let self = this;   
      self.requestProcessing = false;
      let username = localStorage.getItem('aisname');

      self.$v.newSeriesNumber.$touch();
      self.$v.newSeriesDate.$touch();
      self.$v.newBank.$touch();
      self.$v.newBankAccount.$touch();

      if(!self.$v.newSeriesNumber.$error && !self.$v.newSeriesDate.$error && !self.$v.newBank.$error && !self.$v.newBankAccount.$error){

        self.requestProcessing = true;

        let newChequeSeries = {
          bankChequeSeries: self.newSeriesNumber,
          bankChequeSeriesDate: self.newSeriesDate,
          bank: self.newBank,
          bankAccount: self.newBankAccount,
          createdBy: username
        }
        fetch(api + '/banks/cheque/series/new/',{
            method: 'post',
            body: JSON.stringify(newChequeSeries),
            headers: {
              'Content-Type': 'application/json'
            }
        }).then( response => {
          return response.json();
        }).then(data => {
          if(data.success) {
            self.newSeriesNumber = '';
            self.newSeriesDate = '';
            self.$v.newSeriesNumber.$reset();
            self.$v.newSeriesDate.$reset();
            self.$swal({
              title: 'Success',
              text: 'New Cheque Series added successfully!',
              icon: 'success' ,
              confirmButtonColor: '#51cbce',
              confirmButtonText: 'OK'
            }).then(() => {
              self.$bvModal.hide('new-cheque-series-modal');
              self.getActivateBanks();
            });
          } else {
            self.requestProcessing = false;
            self.$swal({
               title: 'Error',
               text: data.errorMessage, //"Unable to create new brand",
               icon: 'error' ,
               confirmButtonColor: '#ef8157',
               confirmButtonText: 'OK'
            });
          }
        }).catch(function() {

        }).then(function() {
          self.requestProcessing = false;
          self.getPaginatedChequeSeries(1, "");
        });
      }
    },

    openUpdateChequeSeriesModal(series){
      this.selectedChequeSeries = {
        chequeSeriesUid: series.bank_cheque_series_uid,
        seriesNumber: series.bank_cheque_series,
        seriesDate: series.bank_cheque_series_date,
        bank: series.bank_reference,
        bankAccount: series.bank_account_reference 
      }

      this.getActivateBankAccounts(series.bank_reference);
      this.$bvModal.show('update-cheque-series-modal')
    },

    updateChequeSeries(){
      let self = this;  
      self.requestProcessing = true;

      self.$v.selectedChequeSeries.$touch();

      if(!self.$v.selectedChequeSeries.$error){
        let updateChequeSeries = {
          chequeSeriesUid: self.selectedChequeSeries.chequeSeriesUid,
          bankChequeSeries: self.selectedChequeSeries.seriesNumber,
          bankChequeSeriesDate: self.selectedChequeSeries.seriesDate,
          bank: self.selectedChequeSeries.bank,
          bankAccount: self.selectedChequeSeries.bankAccount
        }
        
        fetch(api + '/banks/cheque/series/update/', {
          method: 'post',
          body: JSON.stringify(updateChequeSeries),
          headers: {
            'Content-Type': 'application/json'
          }
        }).then( response => {
           return response.json();
        }).then(data => {
           if(data.success) {
              self.$swal({
                 title: 'Success',
                 text: 'Cheque Series updated successfully!',
                 icon: 'success' ,
                 confirmButtonColor: '#51cbce',
                 confirmButtonText: 'OK'
              }).then(() => {
                 self.selectedChequeSeries = {};
                 self.$v.selectedChequeSeries.$reset();
                 self.$bvModal.hide('update-cheque-series-modal');
              });
           } else {
              self.$swal({
                 title: 'Error',
                 text: data.errorMessage,
                 icon: 'error' ,
                 confirmButtonColor: '#ef8157',
                 confirmButtonText: 'OK'
              });
           }
        }).catch(function(error) {
           console.log(error);
        }).then(function() {
            self.requestProcessing = false;
            self.getPaginatedChequeSeries(1, "");
        });
      }
    }
  }
};
</script>