<template>
<div>
	<base-header type="gradient-success" class="pb-6 pb-8 pt-5">
        <!-- Card stats -->
    </base-header>     
    <div class="col-10 container-fluid mt--7" >
        <card>
			<div class="card-header bg-transparent pb-5">
				<div class="text-right">
					
				</div>
			</div>
			<div class="form-group row">
				<label for="chequeType" class="col-sm-4 col-form-label">Cheque For</label>
				<label for="chequeType" class="col-sm-4 col-form-label">Cheque Status</label>
				<label for="chequeDateType" class="col-sm-4 col-form-label">Cheque Date Type</label>
				<div class="col-sm-4">
					<v-select class="form-control-plaintext" :options="chequeTypes" :reduce="chequeType => chequeType.type" label="type" v-model="newChequeType"></v-select>
					<i class="text-danger" v-if="$v.newChequeType.$anyDirty && !$v.newChequeType.required">Field Required</i>
				</div>
				<div class="col-sm-4">
					<v-select class="form-control-plaintext" :options="chequeStatus" :reduce="chequeStatus => chequeStatus.status" label="status" v-model="newChequeStatus"></v-select>
					<i class="text-danger" v-if="$v.newChequeStatus.$anyDirty && !$v.newChequeStatus.required">Field Required</i>
				</div>
				<div class="col-sm-2">
					<base-radio name="dated" class="mb-3" v-model="radioChequeDateType">Dated</base-radio>
				</div>
				<div class="col-sm-2">
					<base-radio name="postdated" class="mb-3" v-model="radioChequeDateType">Postdated</base-radio>
				</div>
			</div>
			<div class="form-group row">
				<label for="bank" class="col-sm-3 col-form-label">Bank</label>
				<div class="col-sm-8">
					<v-select class="form-control-plaintext" :options="activateBanks" :reduce="activateBank => activateBank.bankUid" label="bank" v-model="newBank" @input="getActivateBankAccounts(newBank)"></v-select>
					<i class="text-danger" v-if="$v.newBank.$anyDirty && !$v.newBank.required">Field Required</i>
				</div>
				<label for="bankAccount" class="col-sm-3 col-form-label">Bank Account</label>
				<div class="col-sm-8" v-if="activateBankAccounts == ''" >
					<span class="text-danger"> No Bank Account </span>
				</div>
				<div class="col-sm-8" v-else>
					<v-select class="form-control-plaintext" :options="activateBankAccounts" :reduce="activateBankAccount => activateBankAccount.bankAccountUid" label="bankAccount" v-model="newBankAccount" @input="getChequeSeries(newBank, newBankAccount)"></v-select>
					<i class="text-danger" v-if="$v.newBankAccount.$anyDirty && !$v.newBankAccount.required">Field Required</i>
				</div>
				<label for="chequeSeries" class="col-sm-3 col-form-label">Cheque Series</label>
				<div class="col-sm-8" v-if="chequeSeries == ''">
					<span class="text-danger"> No Cheque Series </span>
				</div>
				<div class="col-sm-8" v-else>
					<v-select class="form-control-plaintext" :options="chequeSeries" :reduce="series => series.bankChequeSeriesUid" label="bankChequeSeries" v-model="newChequeSeries"  @input="getChequeNumber(newBank, newBankAccount, newChequeSeries)"></v-select>
					<i class="text-danger" v-if="$v.newChequeSeries.$anyDirty && !$v.newChequeSeries.required">Field Required</i>
				</div>
				<label for="chequeNumber" class="col-sm-3 col-form-label" v-if="chequeSeries != ''">Cheque Number</label>
				<div class="col-sm-8" v-if="chequeSeries != ''">
					<base-input v-model="newChequeNumber" id="chequeNumber" type="text" class="form-control-plaintext" placeholder="Cheque Number" readonly></base-input>
				</div>
				<label for="voucherNumber" class="col-sm-3 col-form-label">Voucher Number</label>
				<div class="col-sm-8">
					<base-input v-model="newVoucherNumber" id="voucherNumber" type="text" class="form-control-plaintext" placeholder="Voucher Number"></base-input>
					<i class="text-danger" v-if="$v.newVoucherNumber.$anyDirty && !$v.newVoucherNumber.required">Field Required</i>
					<i class="text-danger" v-if="$v.newVoucherNumber.$anyDirty && !$v.newVoucherNumber.numCustomValidator">Invalid Characters</i>
				</div>
				<label for="chequeDate" class="col-sm-3 col-form-label">Cheque Date</label>
				<div class="col-sm-8">
					<base-input v-model="newChequeDate" id="chequeDate" type="date" class="form-control-plaintext" placeholder="Cheque Date"></base-input>
					<i class="text-danger" v-if="$v.newChequeDate.$anyDirty && !$v.newChequeDate.required">Field Required</i>
				</div>
				<label for="amount" class="col-sm-3 col-form-label">Amount</label>
				<div class="col-sm-8">
					<base-input v-model="newAmount" id="amount" type="text" class="form-control-plaintext" placeholder="Amount"></base-input>
					<i class="text-danger" v-if="$v.newAmount.$anyDirty && !$v.newAmount.required">Field Required</i>
					<i class="text-danger" v-if="$v.newAmount.$anyDirty && !$v.newAmount.amountCustomValidator">Invalid Characters</i>
				</div>
				<label for="peripheral" class="col-sm-3 col-form-label">Peripheral</label>
				<div class="col-sm-8">
					<base-input v-model="newPeripheral" id="peripheral" type="text" class="form-control-plaintext" placeholder="Peripheral"></base-input>
					<i class="text-danger" v-if="$v.newPeripheral.$anyDirty && !$v.newPeripheral.required">Field Required</i>
					<i class="text-danger" v-if="$v.newPeripheral.$anyDirty && !$v.newPeripheral.alphaCustomValidator">Invalid Characters</i>
				</div>
			</div>
			<hr>
			<div class="text-center">
				<base-button type="danger" outline class="col-md-2 ml-2" @click.native="cancelNewCheque">Cancel</base-button>
				<base-button type="primary" class="col-md-2 ml-2" @click.native="addCheque" v-if="activateBankAccounts != '' &&  chequeSeries != ''">Save</base-button>
			</div>
		</card>
	</div>
</div>
</template>
<script>
import { mapGetters } from 'vuex';
import { api } from '@/main';
import { helpers, required } from "vuelidate/lib/validators";
const numCustomValidator = helpers.regex('alphaNumAndDot', /^[ 0-9 ]*$/);
const amountCustomValidator = helpers.regex('alphaNumAndDot', /^[ 0-9. ]*$/);
const alphaCustomValidator = helpers.regex('alphaNumAndDot', /^[ A-Za-z ]*$/);

export default {
  components: {
    // PaperTable,
  },
   data() {
    return {
		banks: [],
		activateBanks: [],
		activateBankAccounts: [],
		chequeTypes: [
			{"type": 'P.O'},
			{"type": 'Utility'},
			{"type": 'Others'}
		],
		chequeStatus: [
			{"status": 'Pending'},
			{"status": 'Bank-In'},
			{"status": 'Bounced'},
			{"status": 'Cancelled'}
		],
		chequeSeries: [],

		newChequeNumber: '',
		newVoucherNumber: '',
		newChequeDate: '',
		newAmount: '',
		newPeripheral: '',
		radioChequeDateType: "dated",
		newChequeType : 'P.O',
		newChequeStatus: 'Pending',
		newBank: '',
		newBankAccount: '',
		newChequeSeries: '',
		// bankUid: '',

		requestProcessing: false,
    };
  },

  computed: {
    ...mapGetters({
      // permission: "Settings/permission" ,
    })
  },
  validations: {
    newVoucherNumber: { required, numCustomValidator },
    newChequeDate: { required },
    newAmount: { required, amountCustomValidator },
    newPeripheral: { required, alphaCustomValidator },
    newChequeType: { required },
    newBank: { required },
    newBankAccount: { required },
    newChequeSeries: { required },
    newChequeStatus: { required },
  },
  async mounted(){
    this.getActivateBanks();
    this.checkToken();
  },
  methods: {
    checkToken(){
      if(!localStorage.getItem('aistoken')){
        this.$router.push("/login");
      }
    },
    getActivateBanks(){
      let self = this;
      self.activateBanks = [];

      fetch(api + '/banks/bank/get/activate').then(function(response) {
        return response.json();
      }).then(function(response) {
        if(response.success) {
          self.activateBanks = response.output.banks;
          self.newBank = response.output.banks[0].bankUid;
          self.getActivateBankAccounts(response.output.banks[0].bankUid);
        }
      }).catch(function(error) {
        console.log(error);
        self.loadError = true;
      });
    },

    getActivateBankAccounts(bank){
      let self = this;
      self.activateBankAccounts = [];
      
      fetch(api + '/banks/account/get/activate/' + bank).then(function(response) {
        return response.json();
      }).then(function(response) {
        if(response.success) {
          self.activateBankAccounts = response.output.bankAccounts;
          self.newBankAccount = response.output.bankAccounts[0].bankAccountUid;
          self.getChequeSeries(self.newBank, self.newBankAccount);
        }
      }).catch(function(error) {
        console.log(error);
        self.loadError = true;
      });
    },

    getChequeSeries(bank, bankAccount){
		let self = this;
		self.chequeSeries = [];

		fetch(api + '/banks/cheque/get/all/' + bank + '/' + bankAccount).then(function(response){
			return response.json();
		}).then(function(response) {
			if(response.success) {
				self.chequeSeries = response.output.chequeSeries;
				self.newChequeSeries = response.output.chequeSeries[0].bankChequeSeriesUid;
				self.getChequeNumber(self.newBank, self.newBankAccount, self.newChequeSeries);
			}
		}).catch(function(error) {
			console.log(error);
			self.loadError = true;
		});
    },

    getChequeNumber(bank, bankAccount, series){
      let self = this;
      self.newChequeNumber = '';
      
      fetch(api + '/banks/cheque/number/get/' + bank + '/' + bankAccount + '/' + series).then(function(response) {
        return response.json();
      }).then(function(response) {
        if(response.success) {
          self.newChequeNumber = parseInt(response.output.chequeNumber) + 1;
        }
      }).catch(function(error) {
        console.log(error);
        self.loadError = true;
      });
    },

	cancelNewCheque(){
		this.$router.push("/bank/cheque");
	},

    addCheque(){
		let self = this;   
		self.requestProcessing = false;
		let addError = true;
		let username = localStorage.getItem('aisname');

		self.$v.newVoucherNumber.$touch();
		self.$v.newChequeDate.$touch();
		self.$v.newAmount.$touch();
		self.$v.newPeripheral.$touch();
		self.$v.newChequeType.$touch();
		self.$v.newChequeStatus.$touch();
		self.$v.newBank.$touch();
		self.$v.newBankAccount.$touch();
		self.$v.newChequeSeries.$touch();

		if(!self.$v.newVoucherNumber.$error && !self.$v.newChequeDate.$error && !self.$v.newAmount.$error && !self.$v.newPeripheral.$error && !self.$v.newChequeType.$error && !self.$v.newChequeStatus.error && !self.$v.newBank.$error && !self.$v.newBankAccount.$error && !self.$v.newChequeSeries.$error){
				addError = false;
		}

		if(!addError){
		self.requestProcessing = true;

		let newCheque = {
			chequeNumber: self.newChequeNumber,
			voucherNumber: self.newVoucherNumber,
			chequeDate: self.newChequeDate,
			chequeAmount: self.newAmount,
			chequePeripheral: self.newPeripheral,
			chequeDateType: self.radioChequeDateType,
			chequeType: self.newChequeType,
			chequeStatus: self.newChequeStatus,
			bank: self.newBank,
			bankAccount: self.newBankAccount,
			chequeSeries: self.newChequeSeries,
			createdBy: username
		}
		fetch(api + '/banks/cheque/new/',{
			method: 'post',
			body: JSON.stringify(newCheque),
			headers: {
				'Content-Type': 'application/json'
			}
		}).then( response => {
			return response.json();
		}).then(data => {
			if(data.success) {
				self.$swal({
						title: 'Success',
						text: 'New Cheque added successfully!',
						icon: 'success' ,
						confirmButtonColor: '#51cbce',
						confirmButtonText: 'OK'
					}).then(() => {
						self.newChequeNumber = '';
						self.newVoucherNumber = '';
						self.newChequeDate = '';
						self.newAmount = '';
						self.newPeripheral = '';
						self.$v.newVoucherNumber.$reset();
						self.$v.newChequeDate.$reset();
						self.$v.newAmount.$reset();
						self.$v.newPeripheral.$reset();
						self.$v.newChequeType.$reset();
						self.$v.newChequeStatus.$reset();
						self.$v.newBank.$reset();
						self.$v.newBankAccount.$reset();
						self.getActivateBanks();
						self.$router.push("/bank/cheque");
					});
				} else {
					self.requestProcessing = false;
					self.$swal({
						title: 'Error',
						text: data.errorMessage, //"Unable to create new brand",
						icon: 'error' ,
						confirmButtonColor: '#ef8157',
						confirmButtonText: 'OK'
					});
				}
			}).catch(function() {

			}).then(function() {
				self.requestProcessing = false;
			});
		}
    },
  }
};
</script>