<template>
<div>
	<base-header type="gradient-success" class="pb-6 pb-8 pt-5">
        <!-- Card stats -->
    </base-header>     
    <div class="col-10 container-fluid mt--7" >
        <card>
			<div class="card-header bg-transparent pb-5">
				<div class="row">
					<h3 class="text-green">Cheque #{{ cheque.bankChequeNumber }}</h3>
				</div>
				<div class="row">
					<span> Status: {{ cheque.chequeStatus }}</span>
				</div>
			</div>
			<div class="row">
				<label class="col-sm-4"><b>Cheque For: </b></label>
					<span class="col-sm-8">{{ cheque.bankChequeType }}</span>
			</div>
			<div class="row">
				<label class="col-sm-4"><b>Cheque Date Type: </b></label>
					<span class="col-sm-8">{{ cheque.bankChequeDateType }}</span>
			</div>
			<div class="row" v-for="bank in activateBanks" v-bind:key="bank.bankUid">
				<label v-if="bank.bankUid == cheque.bankReference" class="col-sm-4"><b>Bank: </b></label>
					<span v-if="bank.bankUid == cheque.bankReference" class="col-sm-8">
						{{ bank.bank }}
					</span>
			</div>
			<div class="row">
				<label class="col-sm-4"><b>Bank Account: </b></label>
					<span v-for="bankAccount in activateBankAccounts" v-bind:key="bankAccount.bankAccountUid" class="col-sm-8">
						<span v-if="bankAccount.bankAccountUid == cheque.bankAccountReference"> {{ bankAccount.bankAccount }}</span>
					</span>
			</div>
			<div class="row">
				<label class="col-sm-4"><b>Series: </b></label>
					<span v-for="series in chequeSeries" v-bind:key="series.bankChequeSeriesUid" class="col-sm-8">
						<span v-if="series.bankChequeSeriesUid == cheque.bankChequeSeriesReference"> {{ series.bankChequeSeries }}</span>
					</span>
			</div>
			<div class="row">
				<label class="col-sm-4"><b>Voucher Number: </b></label>
					<span class="col-sm-8">{{ cheque.voucherNumber }}</span>
			</div>
			<div class="row">
				<label class="col-sm-4"><b>Cheque Date: </b></label>
					<span class="col-sm-8">{{ cheque.bankChequeDate | moment('MMM DD, YYYY') }}</span>
			</div>
			<div class="row">
				<label class="col-sm-4"><b>Amount: </b></label>
					<span class="col-sm-8">{{ cheque.bankChequeAmount }}</span>
			</div>
			<div class="row">
				<label class="col-sm-4"><b>Peripheral: </b></label>
					<span class="col-sm-8">{{ cheque.bankChequePeripheral }}</span>
			</div>
			<hr>
			<div class="text-right">
				<base-button type="danger" outline class="col-md-2 ml-2" @click.native="cancelViewCheque">Cancel</base-button>
				<base-button type="primary" class="col-md-2 ml-2" @click.native="updateCheque">Update</base-button>
			</div>
		</card>
	</div>
</div>
</template>
<script>
import { mapGetters } from 'vuex';
import { api } from '@/main';

export default {
  components: {
    // PaperTable,
  },
   data() {
    return {
		cheque: [],
		activateBanks: [],
		activateBankAccounts: [],
		chequeSeries: [],
    };
  },

  computed: {
    ...mapGetters({
      // permission: "Settings/permission" ,
    })
  },
  async mounted(){
    this.getCheque(this.$router.currentRoute.params.id);
    this.checkToken();
  },
  methods: {
    checkToken(){
      if(!localStorage.getItem('aistoken')){
        this.$router.push("/login");
      }
    },
    getActivateBanks(){
      let self = this;
      self.activateBanks = [];

      fetch(api + '/banks/bank/get/activate').then(function(response) {
        return response.json();
      }).then(function(response) {
        if(response.success) {
          self.activateBanks = response.output.banks;
        }
      }).catch(function(error) {
        console.log(error);
        self.loadError = true;
      });
    },

    getActivateBankAccounts(bank){
      let self = this;
      self.activateBankAccounts = [];
      
      fetch(api + '/banks/account/get/activate/' + bank).then(function(response) {
        return response.json();
      }).then(function(response) {
        if(response.success) {
          self.activateBankAccounts = response.output.bankAccounts;
        }
      }).catch(function(error) {
        console.log(error);
        self.loadError = true;
      });
    },

    getChequeSeries(bank, bankAccount){
		let self = this;
		self.chequeSeries = [];

		fetch(api + '/banks/cheque/get/all/' + bank + '/' + bankAccount).then(function(response){
			return response.json();
		}).then(function(response) {
			if(response.success) {
				self.chequeSeries = response.output.chequeSeries;
			}
		}).catch(function(error) {
			console.log(error);
			self.loadError = true;
		});
    },

    getCheque(chequeID){
		if(chequeID == ''){
			self.$swal({
				title: 'Warning',
				text: 'The cheque ID is missing.',
				icon: 'warning' ,
				confirmButtonColor: '#ef8157',
				confirmButtonText: 'OK'
			}).then(() => {
				self.cancelUpdateCheque();
			});
		}else if (chequeID != '') {
			let self = this;

			fetch(api + '/banks/cheque/get/' + chequeID).then(function(response){
				return response.json();
			}).then(function(response) {
				if(response.success) {
					self.getActivateBanks();
					self.getActivateBankAccounts(response.output.cheque.bankReference);
					self.getChequeSeries(response.output.cheque.bankReference, response.output.cheque.bankAccountReference);
					self.cheque = response.output.cheque;
				}
			}).catch(function(error) {
				console.log(error);
				self.loadError = true;
			});
		}
    },

	cancelViewCheque(){
		this.$router.push("/bank/cheque");
	},

	updateCheque(){
		this.$router.push("/update/cheque/" + this.$router.currentRoute.params.id);
	}
  }
};
</script>