<template>
<div>
	<base-header type="gradient-success" class="pb-6 pb-8 pt-5">
        <!-- Card stats -->
    </base-header>     
    <div class="col-10 container-fluid mt--7" >
        <card>
			<div class="card-header bg-transparent pb-5">
				<div class="text-right">
					
				</div>
			</div>
			<div class="form-group row">
				<label for="paymentType" class="col-sm-4 col-form-label">Cheque For</label>
				<label for="paymentType" class="col-sm-4 col-form-label">Cheque Status</label>
				<label for="chequeType" class="col-sm-4 col-form-label">Cheque Date Type</label>
				<div class="col-sm-4">
					<v-select class="form-control-plaintext" :options="chequeTypes" :reduce="chequeType => chequeType.type" label="type" v-model="chequeType"></v-select>
					<i class="text-danger" v-if="$v.chequeType.$anyDirty && !$v.chequeType.required">Field Required</i>
				</div>
				<div class="col-sm-4">
					<v-select class="form-control-plaintext" :options="chequeStatus" :reduce="chequeStatus => chequeStatus.status" label="status" v-model="chequeStat"></v-select>
					<i class="text-danger" v-if="$v.chequeStat.$anyDirty && !$v.chequeStat.required">Field Required</i>
				</div>
				<div class="col-sm-2">
					<base-radio name="dated" class="mb-3" v-model="radioChequeDateType">Dated</base-radio>
				</div>
				<div class="col-sm-2">
					<base-radio name="postdated" class="mb-3" v-model="radioChequeDateType">Postdated</base-radio>
				</div>
			</div>
			<div class="form-group row">
				<label for="bank" class="col-sm-3 col-form-label">Bank</label>
				<div class="col-sm-8">
					<v-select class="form-control-plaintext" :options="activateBanks" :reduce="activateBank => activateBank.bankUid" label="bank" v-model="bank" @input="getActivateBankAccounts(bank, 'yes')"></v-select>
					<i class="text-danger" v-if="$v.bank.$anyDirty && !$v.bank.required">Field Required</i>
				</div>
				<label for="bankAccount" class="col-sm-3 col-form-label">Bank Account</label>
				<div class="col-sm-8" v-if="activateBankAccounts == ''" >
					<span class="text-danger"> No Bank Account </span>
				</div>
				<div class="col-sm-8" v-else>
					<v-select class="form-control-plaintext" :options="activateBankAccounts" :reduce="activateBankAccount => activateBankAccount.bankAccountUid" label="bankAccount" v-model="bankAccount" @input="getChequeSeries(bank, bankAccount, 'yes')"></v-select>
					<i class="text-danger" v-if="$v.bankAccount.$anyDirty && !$v.bankAccount.required">Field Required</i>
				</div>
				<label for="chequeSeries" class="col-sm-3 col-form-label">Cheque Series</label>
				<div class="col-sm-8" v-if="chequeSeries == ''">
					<span class="text-danger"> No Cheque Series </span>
				</div>
				<div class="col-sm-8" v-else>
					<v-select class="form-control-plaintext" :options="chequeSeries" :reduce="cSeries => cSeries.bankChequeSeriesUid" label="bankChequeSeries" v-model="series"></v-select>
					<i class="text-danger" v-if="$v.series.$anyDirty && !$v.series.required">Field Required</i>
				</div>
				<label for="chequeNumber" class="col-sm-3 col-form-label" v-if="series != ''">Cheque Number</label>
				<div class="col-sm-8" v-if="chequeSeries != ''">
					<base-input v-model="chequeNumber" id="chequeNumber" type="text" class="form-control-plaintext" placeholder="Cheque Number" readonly></base-input>
				</div>
				<label for="voucherNumber" class="col-sm-3 col-form-label">Voucher Number</label>
				<div class="col-sm-8">
					<base-input v-model="voucherNumber" id="voucherNumber" type="text" class="form-control-plaintext" placeholder="Voucher Number"></base-input>
					<i class="text-danger" v-if="$v.voucherNumber.$anyDirty && !$v.voucherNumber.required">Field Required</i>
					<i class="text-danger" v-if="$v.voucherNumber.$anyDirty && !$v.voucherNumber.numCustomValidator">Invalid Characters</i>
				</div>
				<label for="chequeDate" class="col-sm-3 col-form-label">Cheque Date</label>
				<div class="col-sm-8">
					<base-input v-model="chequeDate" id="chequeDate" type="date" class="form-control-plaintext" placeholder="Cheque Date"></base-input>
					<i class="text-danger" v-if="$v.chequeDate.$anyDirty && !$v.chequeDate.required">Field Required</i>
				</div>
				<label for="amount" class="col-sm-3 col-form-label">Amount</label>
				<div class="col-sm-8">
					<base-input v-model="amount" id="amount" type="text" class="form-control-plaintext" placeholder="Amount"></base-input>
					<i class="text-danger" v-if="$v.amount.$anyDirty && !$v.amount.required">Field Required</i>
					<i class="text-danger" v-if="$v.amount.$anyDirty && !$v.amount.amountCustomValidator">Invalid Characters</i>
				</div>
				<label for="peripheral" class="col-sm-3 col-form-label">Peripheral</label>
				<div class="col-sm-8">
					<base-input v-model="peripheral" id="peripheral" type="text" class="form-control-plaintext" placeholder="Peripheral"></base-input>
					<i class="text-danger" v-if="$v.peripheral.$anyDirty && !$v.peripheral.required">Field Required</i>
					<i class="text-danger" v-if="$v.peripheral.$anyDirty && !$v.peripheral.alphaCustomValidator">Invalid Characters</i>
				</div>
			</div>
			<hr>
			<div class="text-center">
				<base-button type="danger" outline class="col-md-2 ml-2" @click.native="cancelUpdateCheque">Cancel</base-button>
				<base-button type="primary" class="col-md-2 ml-2" @click.native="updateCheque" v-if="activateBankAccounts != '' &&  chequeSeries != ''">Save</base-button>
			</div>
		</card>
	</div>
</div>
</template>
<script>
import { mapGetters } from 'vuex';
import { api } from '@/main';
import { helpers, required } from "vuelidate/lib/validators";
const numCustomValidator = helpers.regex('alphaNumAndDot', /^[ 0-9 ]*$/);
const amountCustomValidator = helpers.regex('alphaNumAndDot', /^[ 0-9. ]*$/);
const alphaCustomValidator = helpers.regex('alphaNumAndDot', /^[ A-Za-z ]*$/);

export default {
  components: {
    // PaperTable,
  },
   data() {
    return {
		banks: [],
		activateBanks: [],
		activateBankAccounts: [],
		chequeTypes: [
			{"type": 'P.O'},
			{"type": 'Utility'},
			{"type": 'Others'}
		],
		chequeStatus: [
			{"status": 'Pending'},
			{"status": 'Bank-In'},
			{"status": 'Bounced'},
			{"status": 'Cancelled'}
		],
		chequeSeries: [],

		chequeNumber: '',
		voucherNumber: '',
		chequeDate: '',
		amount: '',
		peripheral: '',
		radioChequeDateType: "",
		chequeType : '',
		chequeStat: '',
		bank: '',
		bankAccount: '',
		series: '',
		// bankUid: '',

		requestProcessing: false,
    };
  },

  computed: {
    ...mapGetters({
      // permission: "Settings/permission" ,
    })
  },
  validations: {
    voucherNumber: { required, numCustomValidator },
    chequeDate: { required },
    amount: { required, amountCustomValidator },
    peripheral: { required, alphaCustomValidator },
    chequeType: { required },
    chequeStat: { required },
    bank: { required },
    bankAccount: { required },
    series: { required },
  },
  async mounted(){
    this.getCheque(this.$router.currentRoute.params.id);
    this.checkToken();
  },
  methods: {
    checkToken(){
      if(!localStorage.getItem('aistoken')){
        this.$router.push("/login");
      }
    },
    getActivateBanks(change){
      let self = this;
      self.activateBanks = [];

      fetch(api + '/banks/bank/get/activate').then(function(response) {
        return response.json();
      }).then(function(response) {
        if(response.success) {
          self.activateBanks = response.output.banks;
          self.getActivateBankAccounts(self.bank, change);
        }
      }).catch(function(error) {
        console.log(error);
        self.loadError = true;
      });
    },

    getActivateBankAccounts(bank, change){
      let self = this;
      self.activateBankAccounts = [];
      
      fetch(api + '/banks/account/get/activate/' + bank).then(function(response) {
        return response.json();
      }).then(function(response) {
        if(response.success) {
          self.activateBankAccounts = response.output.bankAccounts;
          self.bankAccount = response.output.bankAccounts[0].bankAccountUid;
          self.getChequeSeries(self.bank, self.bankAccount, change);
        }
      }).catch(function(error) {
        console.log(error);
        self.loadError = true;
      });
    },

    getChequeSeries(bank, bankAccount, change){
		let self = this;
		self.chequeSeries = [];

		fetch(api + '/banks/cheque/get/all/' + bank + '/' + bankAccount).then(function(response){
			return response.json();
		}).then(function(response) {
			if(response.success) {
				self.chequeSeries = response.output.chequeSeries;
				self.series = response.output.chequeSeries[0].bankChequeSeriesUid;
				if(change == 'yes'){
					self.getChequeNumber(self.bank, self.bankAccount, self.series);
				}
			}
		}).catch(function(error) {
			console.log(error);
			self.loadError = true;
		});
    },

    getChequeNumber(bank, bankAccount, series){
      let self = this;
      
      fetch(api + '/banks/cheque/number/get/' + bank + '/' + bankAccount + '/' + series).then(function(response) {
        return response.json();
      }).then(function(response) {
        if(response.success) {
          self.chequeNumber = parseInt(response.output.chequeNumber) + 1;
        }
      }).catch(function(error) {
        console.log(error);
        self.loadError = true;
      });
    },

    getCheque(chequeID){
		if(chequeID == ''){
			self.$swal({
				title: 'Warning',
				text: 'The cheque ID is missing.',
				icon: 'warning' ,
				confirmButtonColor: '#ef8157',
				confirmButtonText: 'OK'
			}).then(() => {
				self.cancelUpdateCheque();
			});
		}else if (chequeID != '') {
			let self = this;

			fetch(api + '/banks/cheque/get/' + chequeID).then(function(response){
				return response.json();
			}).then(function(response) {
				if(response.success) {
					self.chequeNumber = response.output.cheque.bankChequeNumber;
					self.voucherNumber = response.output.cheque.voucherNumber;
					self.chequeDate = response.output.cheque.bankChequeDate;
					self.amount = response.output.cheque.bankChequeAmount;
					self.peripheral = response.output.cheque.bankChequePeripheral;
					self.radioChequeDateType = response.output.cheque.bankChequeDateType;
					self.chequeType = response.output.cheque.bankChequeType;
					self.chequeStat = response.output.cheque.chequeStatus;
					self.bank = response.output.cheque.bankReference;
					self.bankAccount = response.output.cheque.bankAccountReference;
					self.series = response.output.cheque.bankChequeSeriesReference;
					self.getActivateBanks('no');
				}
			}).catch(function(error) {
				console.log(error);
				self.loadError = true;
			});
		}
    },

	cancelUpdateCheque(){
		this.$router.push("/bank/cheque");
	},

    updateCheque(){
		let self = this;   
		self.requestProcessing = false;
		let addError = true;

		self.$v.voucherNumber.$touch();
		self.$v.chequeDate.$touch();
		self.$v.amount.$touch();
		self.$v.peripheral.$touch();
		self.$v.chequeType.$touch();
		self.$v.chequeStat.$touch();
		self.$v.bank.$touch();
		self.$v.bankAccount.$touch();
		self.$v.series.$touch();

		if(!self.$v.voucherNumber.$error && !self.$v.chequeDate.$error && !self.$v.amount.$error && !self.$v.peripheral.$error && !self.$v.chequeType.$error && !self.$v.chequeStat.$error && !self.$v.bank.$error && !self.$v.bankAccount.$error && !self.$v.series.$error){
			addError = false;
		}

		if(!addError){
		self.requestProcessing = true;

		let updateCheque = {
			chequeUid: self.$router.currentRoute.params.id,
			chequeNumber: self.chequeNumber,
			voucherNumber: self.voucherNumber,
			chequeDate: self.chequeDate,
			chequeAmount: self.amount,
			chequePeripheral: self.peripheral,
			chequeDateType: self.radioChequeDateType,
			chequeType: self.chequeType,
			chequeStatus: self.chequeStat,
			bank: self.bank,
			bankAccount: self.bankAccount,
			chequeSeries: self.series
		}
		fetch(api + '/banks/cheque/update/',{
			method: 'post',
			body: JSON.stringify(updateCheque),
			headers: {
				'Content-Type': 'application/json'
			}
		}).then( response => {
			return response.json();
		}).then(data => {
			if(data.success) {
				self.$swal({
						title: 'Success',
						text: 'Cheque updated successfully!',
						icon: 'success' ,
						confirmButtonColor: '#51cbce',
						confirmButtonText: 'OK'
					}).then(() => {
						self.$router.push("/view/cheque/" + self.$router.currentRoute.params.id);
					});
				} else {
					self.requestProcessing = false;
					self.$swal({
						title: 'Error',
						text: data.errorMessage, //"Unable to create new brand",
						icon: 'error' ,
						confirmButtonColor: '#ef8157',
						confirmButtonText: 'OK'
					});
				}
			}).catch(function() {

			}).then(function() {
				self.requestProcessing = false;
			});
		}
    },
  }
};
</script>