<template>
<div>
	<base-header type="gradient-success" class="pb-6 pb-8 pt-5">
        <!-- Card stats -->
    </base-header>     
    <div class="col-10 container-fluid mt--7" >
        <card>
			<div class="card-header bg-transparent pb-5">
				<div class="text-right">
					
				</div>
			</div>
			<div class="form-group row">
				<label for="firstname" class="col-sm-3 col-form-label">Firstname</label>
				<div class="col-sm-8">
					<base-input v-model="newFirstname" id="firstname" type="text" class="form-control-plaintext" placeholder="Firstname"></base-input>
					<i class="text-danger" v-if="$v.newFirstname.$anyDirty && !$v.newFirstname.required">Field Required</i>
					<i class="text-danger" v-if="$v.newFirstname.$anyDirty && !$v.newFirstname.alphaCustomValidator">Invalid Characters</i>
				</div>
				<label for="lastname" class="col-sm-3 col-form-label">Lastname</label>
				<div class="col-sm-8">
					<base-input v-model="newLastname" id="firstname" type="text" class="form-control-plaintext" placeholder="Lastname"></base-input>
					<i class="text-danger" v-if="$v.newLastname.$anyDirty && !$v.newLastname.required">Field Required</i>
					<i class="text-danger" v-if="$v.newLastname.$anyDirty && !$v.newLastname.alphaCustomValidator">Invalid Characters</i>
				</div>
				<label for="emailAddress" class="col-sm-3 col-form-label">Email Address</label>
				<div class="col-sm-8">
					<base-input v-model="newEmailAddress" id="emailAddress" type="text" class="form-control-plaintext" placeholder="Email Address"></base-input>
					<i class="text-danger" v-if="$v.newEmailAddress.$anyDirty && !$v.newEmailAddress.required">Field Required</i>
					<i class="text-danger" v-if="$v.newEmailAddress.$anyDirty && !$v.newEmailAddress.email">Invalid Characters</i>
				</div>
				<label for="contactNumber" class="col-sm-3 col-form-label">Contact Number</label>
				<div class="col-sm-8">
					<base-input v-model="newContactNumber" id="contactNumber" type="text" class="form-control-plaintext" placeholder="Contact Number"></base-input>
					<i class="text-danger" v-if="$v.newContactNumber.$anyDirty && !$v.newContactNumber.required">Field Required</i>
					<i class="text-danger" v-if="$v.newContactNumber.$anyDirty && !$v.newContactNumber.numCustomValidator">Invalid Characters</i>
				</div>
				<label for="username" class="col-sm-3 col-form-label">Username</label>
				<div class="col-sm-8">
					<base-input v-model="newUsername" id="username" type="text" class="form-control-plaintext" placeholder="Username"></base-input>
					<i class="text-danger" v-if="$v.newUsername.$anyDirty && !$v.newUsername.required">Field Required</i>
				</div>
				<label for="password" class="col-sm-3 col-form-label">Password</label>
				<div class="col-sm-8">
					<base-input v-model="newPassword" id="password" type="password" class="form-control-plaintext" placeholder="Password"></base-input>
					<i class="text-danger" v-if="$v.newPassword.$anyDirty && !$v.newPassword.required">Field Required</i>
				</div>
				<label for="userRole" class="col-sm-3 col-form-label">User Role</label>
				<div class="col-sm-8">
					<v-select class="form-control-plaintext" :options="userRoles" :reduce="role => role.userRoleUid" label="userRole" v-model="newUserRole"></v-select>
					<i class="text-danger" v-if="$v.newUserRole.$anyDirty && !$v.newUserRole.required">Field Required</i>
				</div>
				
			</div>
			<hr>
			<div class="text-center">
				<base-button type="danger" outline class="col-md-2 ml-2" @click.native="cancelNewUser">Cancel</base-button>
				<base-button type="primary" class="col-md-2 ml-2" @click.native="addUser">Save</base-button>
			</div>
		</card>
	</div>
</div>
</template>
<script>
import { mapGetters } from 'vuex';
import { api } from '@/main';
import { helpers, required, email } from "vuelidate/lib/validators";
const numCustomValidator = helpers.regex('alphaNumAndDot', /^[ 0-9 ]*$/);
const alphaCustomValidator = helpers.regex('alphaNumAndDot', /^[ A-Za-z ]*$/);

export default {
  components: {
    // PaperTable,
  },
   data() {
    return {
		userRoles: [],

		newFirstname: '',
		newLastname: '',
		newEmailAddress: '',
		newContactNumber: '',
		newUsername: '',
		newPassword : '',
		newUserRole: '',

		requestProcessing: false,
    };
  },

  computed: {
    ...mapGetters({
      // permission: "Settings/permission" ,
    })
  },
  validations: {
    newFirstname: { required, alphaCustomValidator },
    newLastname: { required, alphaCustomValidator },
    newEmailAddress: { required, email },
    newContactNumber: { required, numCustomValidator },
    newUsername: { required },
    newPassword: { required },
    newUserRole: { required }
  },
  async mounted(){
    this.checkToken();
  },
  methods: {
    checkToken(){
      if(!localStorage.getItem('aistoken')){
        this.$router.push("/login");
      }else{
        this.getRoles();
      }
    },
    getRoles(){
      let self = this;

      fetch(api + '/users/roles').then(function(response) {
        return response.json();
      }).then(function(response) {
        if(response.success) {
          self.userRoles = response.output.userRoles;
        }
      }).catch(function(error) {
        console.log(error);
        self.loadError = true;
      });
    },

    addUser(){
		let self = this;   
		self.requestProcessing = false;
		let addError = true;
		let username = localStorage.getItem('aisname');

		self.$v.newFirstname.$touch();
		self.$v.newLastname.$touch();
		self.$v.newEmailAddress.$touch();
		self.$v.newContactNumber.$touch();
		self.$v.newUsername.$touch();
		self.$v.newPassword.$touch();
		self.$v.newUserRole.$touch();

		if(!self.$v.newFirstname.$error && !self.$v.newLastname.$error && !self.$v.newEmailAddress.$error && !self.$v.newContactNumber.$error && !self.$v.newUsername.$error && !self.$v.newPassword.error && !self.$v.newUserRole.$error){
				addError = false;
		}

		if(!addError){
		self.requestProcessing = true;

		let newUser = {
			firstname: self.newFirstname,
			lastname: self.newLastname,
			emailAddress: self.newEmailAddress,
			contactNumber: self.newContactNumber,
			username: self.newUsername,
			password: self.newPassword,
			userRole: self.newUserRole,
			createdBy: username
		}
		fetch(api + '/users/add/new/',{
			method: 'post',
			body: JSON.stringify(newUser),
			headers: {
				'Content-Type': 'application/json'
			}
		}).then( response => {
			return response.json();
		}).then(data => {
			if(data.success) {
				self.$swal({
						title: 'Success',
						text: 'New User added successfully!',
						icon: 'success' ,
						confirmButtonColor: '#51cbce',
						confirmButtonText: 'OK'
					}).then(() => {
						self.newFirstname = '';
						self.newLastname = '';
						self.newEmailAddress = '';
						self.newContactNumber = '';
						self.newUsername = '';
						self.newPassword = '';
						self.$v.newFirstname.$reset();
						self.$v.newLastname.$reset();
						self.$v.newEmailAddress.$reset();
						self.$v.newContactNumber.$reset();
						self.$v.newUsername.$reset();
						self.$v.newPassword.$reset();
						self.$v.newUserRole.$reset();
						self.getRoles();
						self.$router.push("/users");
					});
				} else {
					self.requestProcessing = false;
					self.$swal({
						title: 'Error',
						text: data.errorMessage, //"Unable to create new brand",
						icon: 'error' ,
						confirmButtonColor: '#ef8157',
						confirmButtonText: 'OK'
					});
				}
			}).catch(function() {

			}).then(function() {
				self.requestProcessing = false;
			});
		}
    },
  }
};
</script>