<template>

    <div> 
      <base-header type="gradient-success" class="pb-6 pb-8 pt-5">
            <!-- Card stats -->
      </base-header>     
      <div class="col-12 container-fluid mt--7" >
        <card>
        <div class="card-header bg-transparent pb-5">
          <div class="text-left">
            <h4>User List</h4>
          </div>       
          <div class="text-right">
              <base-button 
                @click.native="newUser" outline type="primary">
                New User
              </base-button>
          </div> 
        </div>

          <div class="input-group table-search">
            <input type="text" v-model="searchTerm" class="form-control" @input="resetData" v-on:keyup.enter="search" name="search" :placeholder="'Search Cheque Number.....'"> 
            <span v-if="searching" class="input-group-append" @click="reloadData">
              <i class="input-group-text ti-close text-danger"></i>
            </span>
            <span v-else class="input-group-append" @click="search">
              <base-button type="primary" icon="fa fa-search"></base-button>
            </span>
          </div>
          <div class="table-full-width">
             <paper-table 
               type="hover"
              :loading="loading"
              :loadError="loadError"
              @reloadData="reloadData"
              :title="userTable.title"
              :placeholder="'Search Username..'"
              :sub-title="userTable.subTitle"
              :data="users"
              :columns="userTable.columns"
              :search="true">
                <template slot-scope="{ row }">
                  <td>{{row.rowNum}}</td>
                  <td>
                    <router-link :to='"/view/user/" + row.user_uid'>{{ row.username }}</router-link>
                  </td>
                  <td>
                    <span v-for="role in roles" v-bind:key="role.userRoleUid" >
                      <span v-if="role.userRoleUid == row.user_role_reference"> {{ role.userRole }}</span>
                    </span>
                  </td>
                  <td>{{ row.email_address }}</td>
                  <td>
                    {{ row.status == 1 ? 'Active' : 'Disabled'}}
                  </td>
                  <td align="right" v-if="row.status == 1">
                    <span class="pointer ml-2 far fa-times-circle text-red" v-on:click="deactivateBank(row)" title="Disable"></span>
                  </td>
                  <td align="right" v-else>
                    <span class="pointer ml-2 far fa-check-circle text-green" v-on:click="activateBank(row)" title="Activate"></span>
                  </td>
                </template>
            </paper-table>
          </div>

          <hr>
          <div class="row" v-if="!loading && users.length">
            <div class="col-8">
              <span class="pagination-results">{{users[users.length - 1].rowNum}} of {{usersCount}}</span>
            </div>
            <pagination
              :searching="searching"
              :searchTotalPage="users"
              :totalPages="usersCount"
              :currentPage="currentPage"
              @pagechanged="onPageChange">
            </pagination>
          </div>
        </card>
      </div>
    </div>
</template>
<script>
import { mapGetters } from 'vuex';
import { api } from '@/main';
// import { helpers, required } from "vuelidate/lib/validators";
// const alphaNumCustomValidator = helpers.regex('alphaNumAndDot', /^[ A-Za-z0-9. ]*$/);
const tableColumns = ["#", "Username", "Role", "Email", "Status", "Actions"];

export default {
  components: {
    // PaperTable,
  },
   data() {
    return {
      loading: true,
      loadError: false,
      userTable: {
        title: "Users Table",
        subTitle: "",
        columns: [...tableColumns],
      },

      usersCount: 0,
      users: [],
      roles: [],

      currentPage: 1 ,
      searchTerm: '',
      searching: false ,

      requestProcessing: false,
    };
  },

  computed: {
    ...mapGetters({
      // permission: "Settings/permission" ,
    })
  },
  async mounted(){
    this.checkToken();
  },
  methods: {
    checkToken(){
      if(!localStorage.getItem('aistoken')){
        this.$router.push("/login");
      }else{
        this.getPaginatedUsers(1, "");
        this.getUserRole();
        this.getRoles();
      }
    },

    getUserRole(){
      let self = this;
      let username = localStorage.getItem('aisname');

      fetch(api + '/users/role/' + username).then(function(response){
        return response.json();
      }).then(function(response) {
        if(response.success) {
          response.output;
          if(response.output != 'Administrator'){
            self.$router.push("/dashboard");
          }
        }
      }).catch(function(error) {
        console.log(error);
        self.loadError = true;
      });
    },

    /* paginate */
    getPaginatedUsers(page, searchTerm) {
      let self = this;
      self.loading = true;
      self.loadError = false;
      self.users = [];

      if(searchTerm) {
        self.searching = true;
      }

      fetch(api + '/users/get/accounts/' + page + '/' + searchTerm).then(function(response) {
        return response.json();
      }).then(function(response) {
        if(response.success) {
          let rowNum = (10 * page) - 9;
          for(var i = 0; i < response.output.users.length; i++){
            response.output.users[i]["rowNum"] = rowNum + i;
          }

          self.currentPage = page;
          self.users = response.output.users;
          self.usersCount = response.output.count;
        }
      }).catch(function() {
        self.loadError = true;
      }).then(function() {
        setTimeout(function() {
          self.loading = false;
        }, 1500);            
      });
    },

    onPageChange (page) {
      let searchTerm = this.searchTerm;

      this.getPaginatedUsers(page, searchTerm);
    } ,

    resetData() {
      this.searching = false;
        if(this.searchTerm == "") {
          this.getPaginatedUsers(1, "");
      }
    },

    reloadData() {
      this.searchTerm = "";
      this.searching = false;
      this.getPaginatedUsers(1, "");
    },

    /* Search */
    search() {
      let searchTerm = this.searchTerm;
      if(searchTerm) {
        this.getPaginatedUsers(1, searchTerm);
      }
    },

    getRoles(){
      let self = this;
      self.roles = [];

      fetch(api + '/users/roles').then(function(response) {
        return response.json();
      }).then(function(response) {
        if(response.success) {
          self.roles = response.output.userRoles;
        }
      }).catch(function(error) {
        console.log(error);
        self.loadError = true;
      });
    },

    newUser(){
      this.$router.push("/new/user");
    },
  }
};
</script>