<template>
    <div>
        <base-header type="gradient-success" class="pb-6 pb-8 pt-5 pt-md-8">
            <!-- Card stats -->
            <div class="row">
                <div class="col-xl-4 col-lg-6">
                    <stats-card title="Total of"
                                type="gradient-red"
                                :sub-title="numberOfCheque"
                                icon="fas fa-money-check"
                                class="mb-4 mb-xl-0"
                    >

                        <template slot="footer">
                            <span class="text-danger mr-2"><b> {{clearedChequeCount}} Cleared</b></span>
                            <span class="text-nowrap">and</span>
                            <span class="text-info mr-2"><b> {{clearingChequeCount}} Clearing Cheque/s</b></span>
                        </template>
                    </stats-card>
                </div>
            </div>
        </base-header>
        <div class="container-fluid mt--7">

            <!--Tables-->
            <div class="row mt-5">
                <div class="col-xl-7">
                    <div class="card">
                    <div class="card-header border-0">
                      <div class="row align-items-center">
                        <div class="col">
                          <h3 class="mb-0">Clearing Cheques</h3>
                        </div>
                        <!-- <div class="col text-right">
                          <base-button size="sm" type="primary">See all</base-button>
                        </div> -->
                      </div>
                    </div>
                    <div class="table-full-width">
                       <paper-table 
                         type="hover"
                        :loading="loading"
                        :loadError="loadError"
                        :title="clearingTable.title"
                        :sub-title="clearingTable.subTitle"
                        :data="clearingCheques"
                        :columns="clearingTable.columns">
                          <template slot-scope="{ row }">
                            <td> {{row.rowNum}}</td>
                            <td>
                              <router-link :to='"/update/cheque/" + row.bank_cheque_uid'>{{ row.bank_cheque_number }}</router-link>
                            </td>
                            <td align="right">
                              <span v-if="row.bank_cheque_date == dateToday" class="text-danger"><b>Clearing Today </b></span>
                              <span v-else-if="row.bank_cheque_date == dateTom" class="text-success"><b>Clearing Tomorrow </b></span>
                              <span v-else>Clearing on {{row.bank_cheque_date | moment("dddd")}} </span>
                              <span>({{row.bank_cheque_date | moment("MMMM DD, YYYY")}})</span>
                            </td>
                          </template>
                      </paper-table>
                    </div>
                    <hr>
                    <div class="row" v-if="!loading && clearingCheques.length">
                      <div class="col-6">
                      </div>
                      <pagination
                        :searchTotalPage="clearingCheques"
                        :totalPages="clearingChequeCount"
                        :currentPage="currentPage"
                        @pagechanged="onPageChange">
                      </pagination>
                    </div>
                  </div>
                </div>

                <div class="col-xl-5">
                    <div class="card">
                    <div class="card-header border-0">
                      <div class="row align-items-center">
                        <div class="col">
                          <h3 class="mb-0 text-danger">Cleared Cheques</h3>
                        </div>
                        <!-- <div class="col text-right">
                          <base-button size="sm" type="primary">See all</base-button>
                        </div> -->
                      </div>
                    </div>
                    <div class="table-full-width">
                       <paper-table 
                         type="hover"
                        :loading="loading"
                        :loadError="loadError"
                        :title="clearedTable.title"
                        :sub-title="clearedTable.subTitle"
                        :data="clearedCheques"
                        :columns="clearedTable.columns">
                          <template slot-scope="{ row }">
                            <td> {{row.rowNum}}</td>
                            <td>
                              <b><router-link :to='"/update/cheque/" + row.bank_cheque_uid' class="text-danger">{{ row.bank_cheque_number }}</router-link></b>
                            </td>
                            <td align="right" class="text-danger">
                              <span>{{row.bank_cheque_date | moment("MMMM DD, YYYY")}}</span>
                            </td>
                          </template>
                      </paper-table>
                    </div>
                    <hr>
                    <div class="row" v-if="!loading && clearedCheques.length">
                      <div class="col-4">
                      </div>
                      <pagination
                        :searchTotalPage="clearedCheques"
                        :totalPages="clearedChequeCount"
                        :currentPage="currentClearedChequePage"
                        @pagechanged="onClearedChequePageChange">
                      </pagination>
                    </div>
                  </div>
                </div>
            </div>
            <!--End tables-->
        </div>

    </div>
</template>
<script>
  // import { mapGetters } from 'vuex';
  import { api } from '@/main';
  import moment from 'moment';
  const tableColumns = ["#", "Cheque Number", ""];
  const clearedChequeColumns = ["#", "Cheque Number", "Date Cleared"];

  export default {
    components: {
    },
    data() {
      return {
        numberOfCheque: '0',

        clearingCheques: [],
        clearingChequeCount: 1,
        clearedCheques: [],
        clearedChequeCount: 1,
        dateToday: moment().format('YYYY-MM-DD'),
        dateTom: moment().add(1, 'day').format('YYYY-MM-DD'),

        loading: true,
        loadError: false,
        clearingTable: {
          title: "Clearing ChequesTable",
          subTitle: "",
          columns: [...tableColumns],
        },
        clearedTable: {
          title: "Cleared ChequesTable",
          subTitle: "",
          columns: [...clearedChequeColumns],
        },

        currentPage: 1 ,
        currentClearedChequePage: 1,
      };
    },
    async mounted() {
      this.getChequeCount();
      this.getPostdatedCheque(1);
      this.getClearedCheque(1);
      this.checkToken();
    },
    methods: {
      checkToken(){
        if(!localStorage.getItem('aistoken')){
          this.$router.push("/login");
        }
      },
      getChequeCount() {
        let self = this;

        fetch(api + '/banks/cheque/count').then(function(response) {
          return response.json();
        }).then(function(response) {
          if(response.success) {
            self.numberOfCheque = response.output.count.toString() + ' Cheques';
          }
        }).catch(function() {
          // self.loadError = true;
        });
      },

      getPostdatedCheque(page) {
        let self = this;

        fetch(api + '/banks/cheque/postdated/get/' + page).then(function(response) {
          return response.json();
        }).then(function(response) {
          if(response.success) {
            let rowNum = (10 * page) - 9;
            for(var i = 0; i < response.output.clearingCheques.length; i++){
              response.output.clearingCheques[i]["rowNum"] = rowNum + i;
            }

            self.currentPage = page;
            self.clearingCheques = response.output.clearingCheques;
            self.clearingChequeCount = response.output.count;
          }
        }).catch(function() {
          // self.loadError = true;
        }).then(function() {
          setTimeout(function() {
            self.loading = false;
          }, 1500);            
        });
      },

      onPageChange (page) {
        this.getPostdatedCheque(page);
      } ,

      getClearedCheque(page) {
        let self = this;

        fetch(api + '/banks/cheque/cleared/get/' + page).then(function(response) {
          return response.json();
        }).then(function(response) {
          if(response.success) {
            let rowNum = (10 * page) - 9;
            for(var i = 0; i < response.output.clearedCheques.length; i++){
              response.output.clearedCheques[i]["rowNum"] = rowNum + i;
            }

            self.currentPage = page;
            self.clearedCheques = response.output.clearedCheques;
            self.clearedChequeCount = response.output.count;
          }
        }).catch(function() {
          // self.loadError = true;
        }).then(function() {
          setTimeout(function() {
            self.loading = false;
          }, 1500);            
        });
      },

      onClearedChequePageChange (page) {
        this.getClearedCheque(page);
      } ,

    },
  }
</script>
<style></style>
