<template>

    <div> 
      <base-header type="gradient-success" class="pb-6 pb-8 pt-5">
            <!-- Card stats -->
      </base-header>     
      <div class="col-12 container-fluid mt--7" >
        <card>
        <div class="card-header bg-transparent pb-5">
          <div class="text-left">
            <h4>Bank List</h4>
          </div>       
          <div class="text-right">
              <base-button 
                v-b-modal.new-bank-modal outline type="primary">
                New Bank
              </base-button>
          </div> 
        </div>

          <div class="input-group table-search">
            <input type="text" v-model="searchTerm" class="form-control" @input="resetData" v-on:keyup.enter="search" name="search" :placeholder="'Search Bank.....'"> 
            <span v-if="searching" class="input-group-append" @click="reloadData">
              <i class="input-group-text ti-close text-danger"></i>
            </span>
            <span v-else class="input-group-append" @click="search">
              <base-button type="primary" icon="fa fa-search"></base-button>
            </span>
          </div>
          <div class="table-full-width">
             <paper-table 
               type="hover"
              :loading="loading"
              :loadError="loadError"
              @reloadData="reloadData"
              :title="bankTable.title"
              :placeholder="'Search Bank..'"
              :sub-title="bankTable.subTitle"
              :data="banks"
              :columns="bankTable.columns"
              :search="true">
                <template slot-scope="{ row }">
                  <td> {{row.rowNum}}</td>
                  <td>{{ row.bank }}</td>
                  <td >{{ row.date_created | moment("MMM DD, YYYY") }}</td>
                  <td>
                    {{ row.status == 1 ? 'Active' : 'Disabled'}}
                  </td>
                  <td align="right" v-if="row.status == 1">
                    <span class="pointer ml-2 far fa-edit text-blue" v-on:click="openUpdateBankModal(row)" title="Update"></span>
                    <span class="pointer ml-2 far fa-times-circle text-red" v-on:click="deactivateBank(row)" title="Disable"></span>
                  </td>
                  <td align="right" v-else>
                    <span class="pointer ml-2 far fa-check-circle text-green" v-on:click="activateBank(row)" title="Activate"></span>
                  </td>
                </template>
            </paper-table>
          </div>

          <hr>
          <div class="row" v-if="!loading && banks.length">
            <div class="col-8">
              <span class="pagination-results">{{banks[banks.length - 1].rowNum}} of {{banksCount}}</span>
            </div>
            <pagination
              :searching="searching"
              :searchTotalPage="banks"
              :totalPages="banksCount"
              :currentPage="currentPage"
              @pagechanged="onPageChange">
            </pagination>
          </div>
        </card>

         <!-- ADD BANK MODAL -->
        <b-modal id="new-bank-modal" title="New Bank" centered :hide-header-close="true" :no-close-on-backdrop="true" :no-close-on-esc="true">

          <div class="form-group row">
             <label for="bank" class="col-sm-2 col-form-label">Bank</label>
             <div class="col-sm-10">
                <base-input v-model="newBank" id="bank" type="text" class="form-control-plaintext" placeholder="Bank"></base-input>
                   <i class="text-danger" v-if="$v.newBank.$anyDirty && !$v.newBank.required">Field Required</i>
                   <i class="text-danger" v-if="$v.newBank.$anyDirty && !$v.newBank.alphaNumCustomValidator">Invalid Characters</i>
             </div>
          </div>

          <template v-slot:modal-footer>
             <div class="w-100">
                <div class="pull-right">
                   <base-button type="primary" @click.native="addBank" :disabled="requestProcessing">
                      <i v-if="requestProcessing" class="fa fa-spinner fa-spin"></i>
                      Add
                   </base-button>
                   <base-button type="danger" @click.native="$bvModal.hide('new-bank-modal')" :disabled="requestProcessing">Cancel</base-button>
                </div>
            </div>
          </template>
        </b-modal>

        <!-- UPDATE BANK MODAL -->
        <b-modal id="update-bank-modal" title="Update Bank" centered :hide-header-close="true" :no-close-on-backdrop="true" :no-close-on-esc="true">

            <div class="form-group row">
               <label for="bank" class="col-sm-2 col-form-label">Bank</label>
               <div class="col-sm-10">
                  <base-input v-model="selectedBank.bank" type="text" class="form-control-plaintext" placeholder="Bank"></base-input>
                  <i class="text-danger" v-if="$v.selectedBank.$anyDirty && !$v.selectedBank.bank.required">Field Required</i>
                  <i class="text-danger" v-if="$v.selectedBank.$anyDirty && !$v.selectedBank.bank.alphaNumCustomValidator">Invalid Characters</i>
               </div>
            </div>

            <template v-slot:modal-footer>
               <div class="w-100">
                  <div class="pull-right">
                     <base-button type="primary" @click.native="updateBank" :disabled="requestProcessing">
                        <i v-if="requestProcessing" class="fa fa-spinner fa-spin"></i>
                        Update
                     </base-button>
                     <base-button type="danger" @click.native="$bvModal.hide('update-bank-modal')" :disabled="requestProcessing">Cancel</base-button>
                  </div>
              </div>
            </template>
          </b-modal>

      </div>
    </div>
</template>
<script>
import { mapGetters } from 'vuex';
import { api } from '@/main';
import { helpers, required } from "vuelidate/lib/validators";
const alphaNumCustomValidator = helpers.regex('alphaNumAndDot', /^[ A-Za-z . ]*$/);
const tableColumns = ["#", "Bank", "Date Added", "Status", "Actions"];

export default {
  components: {
    // PaperTable,
  },
   data() {
    return {
      loading: true,
      loadError: false,
      bankTable: {
        title: "Banks Table",
        subTitle: "",
        columns: [...tableColumns],
      },

      banksCount: 0,
      banks: [],

      currentPage: 1 ,
      searchTerm: '',
      searching: false ,

      newBank: '',
      selectedBank: {
        bankUid: null,
        bank: '',
      },

      banksUid: '',

      requestProcessing: false,
    };
  },

  computed: {
    ...mapGetters({
      // permission: "Settings/permission" ,
    })
  },
  async mounted(){
    this.getPaginatedBanks(1, "");
    this.checkToken();
  },

  validations: {
    newBank: { required, alphaNumCustomValidator },
    selectedBank: {
      bank: { required, alphaNumCustomValidator }
    },
  },
  methods: {
    /* paginate */
    checkToken(){
      if(!localStorage.getItem('aistoken')){
        this.$router.push("/login");
      }
    },
    getPaginatedBanks(page, searchTerm) {
      let self = this;
      self.loading = true;
      self.loadError = false;
      self.banks = [];

      if(searchTerm) {
        self.searching = true;
      }

      fetch(api + '/banks/bank/paginated/get/' + page + '/' + searchTerm).then(function(response) {
        return response.json();
      }).then(function(response) {
        if(response.success) {
          let rowNum = (10 * page) - 9;
          for(var i = 0; i < response.output.banks.length; i++){
            response.output.banks[i]["rowNum"] = rowNum + i;
          }

          self.currentPage = page;
          self.banks = response.output.banks;
          self.banksCount = response.output.count;
        }
      }).catch(function() {
        self.loadError = true;
      }).then(function() {
        setTimeout(function() {
          self.loading = false;
        }, 1500);            
      });
    },

    onPageChange (page) {
      let searchTerm = this.searchTerm;

      this.getPaginatedBanks(page, searchTerm);
    } ,

    resetData() {
      this.searching = false;
        if(this.searchTerm == "") {
          this.getPaginatedBanks(1, "");
      }
    },

    reloadData() {
      this.searchTerm = "";
      this.searching = false;
      this.getPaginatedBanks(1, "");
    },

    /* Search */
    search() {
      let searchTerm = this.searchTerm;
      if(searchTerm) {
        this.getPaginatedBanks(1, searchTerm);
      }
    } ,

    addBank(){
      let self = this;   
      self.requestProcessing = false;

      self.$v.newBank.$touch();

      if(!self.$v.newBank.$error){

        self.requestProcessing = true;
        let username = localStorage.getItem('aisname');
        let newBank = {
          bank: self.newBank,
          createdBy: username
        }
        fetch(api + '/banks/bank/new/',{
            method: 'post',
            body: JSON.stringify(newBank),
            headers: {
              'Content-Type': 'application/json'
            }
        }).then( response => {
          return response.json();
        }).then(data => {
          if(data.success) {
            self.newBank = '';
            self.$v.newBank.$reset();
            self.$swal({
              title: 'Success',
              text: 'New Bank added successfully!',
              icon: 'success' ,
              confirmButtonColor: '#51cbce',
              confirmButtonText: 'OK'
            }).then(() => {
              self.$bvModal.hide('new-bank-modal');
            });
          } else {
            self.requestProcessing = false;
            self.$swal({
               title: 'Error',
               text: data.errorMessage, //"Unable to create new brand",
               icon: 'error' ,
               confirmButtonColor: '#ef8157',
               confirmButtonText: 'OK'
            });
          }
        }).catch(function() {

        }).then(function() {
          self.requestProcessing = false;
          self.getPaginatedBanks(1, "");
        });
      }
    },

    openUpdateBankModal(bank){
       this.selectedBank = {
         bankUid: bank.bank_uid,
         bank: bank.bank
       }
       this.$bvModal.show('update-bank-modal')
    },

    updateBank(){
      let self = this;  
      self.requestProcessing = true;

      self.$v.selectedBank.$touch();

      if(!self.$v.selectedBank.$error){
        let updateBank = {
          bankUid: self.selectedBank.bankUid,
          bank: self.selectedBank.bank
        }
        
        fetch(api + '/banks/bank/update/', {
          method: 'post',
          body: JSON.stringify(updateBank),
          headers: {
            'Content-Type': 'application/json'
          }
        }).then( response => {
           return response.json();
        }).then(data => {
           if(data.success) {
              self.$swal({
                 title: 'Success',
                 text: 'Bank updated successfully!',
                 icon: 'success' ,
                 confirmButtonColor: '#51cbce',
                 confirmButtonText: 'OK'
              }).then(() => {
                 self.selectedBank = {};
                 self.$v.selectedBank.$reset();
                 self.$bvModal.hide('update-bank-modal');
              });
           } else {
              self.$swal({
                 title: 'Error',
                 text: data.errorMessage,
                 icon: 'error' ,
                 confirmButtonColor: '#ef8157',
                 confirmButtonText: 'OK'
              });
           }
        }).catch(function(error) {
           console.log(error);
        }).then(function() {
           self.requestProcessing = false;
           self.getPaginatedBanks(1, "");
        });
      }
    },

    deactivateBank(bank){
      let self = this;
      let deactivateBank = {
        status: 0,
        bankUid: bank.bank_uid 
      } 
      
      this.$swal({
          title: 'Are you sure?',
          text: "You want to disable this Bank",
          icon: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#51cbce',
          cancelButtonColor: '#ef8157',
          confirmButtonText: 'CONFIRM' ,
          cancelButtonText: 'CANCEL' ,
      }).then((result) => {
        if (result.value) {
          fetch(api + '/banks/bank/status/update',{
            method: 'post',
            body: JSON.stringify(deactivateBank),
            headers: {
              'Content-Type': 'application/json'
            }
          }).then( response => {
            return response.json();
          }).then(data => {
            if(data.success) {
              self.$swal({
                title: 'Success',
                text: 'Bank disabled!',
                icon: 'success' ,
                confirmButtonColor: '#51cbce',
                confirmButtonText: 'OK'
              }).then(() => {
                self.updateBankModal = false;
              });
            } else {
              self.$swal({
                title: 'Error',
                text: data.errorMessage,
                icon: 'error',
                confirmButtonColor: '#ef8157',
                confirmButtonText: 'OK'
              });
            }
          }).catch(function() {

          }).then(function() {
            self.getPaginatedBanks(1, "");
          });
        }
      }); 
    },

    activateBank(bank){
      let self = this;
      let activateBank = {
        status: 1,
        bankUid: bank.bank_uid
      } 
      
      this.$swal({
          title: 'Are you sure?',
          text: "You want to activate this Bank",
          icon: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#51cbce',
          cancelButtonColor: '#ef8157',
          confirmButtonText: 'CONFIRM' ,
          cancelButtonText: 'CANCEL' ,
      }).then((result) => {
        if (result.value) {
          fetch(api + '/banks/bank/status/update',{
            method: 'post',
            body: JSON.stringify(activateBank),
            headers: {
              'Content-Type': 'application/json'
            }
          }).then( response => {
            return response.json();
          }).then(data => {
            if(data.success) {
              self.$swal({
                title: 'Success',
                text: 'Bank activate!',
                icon: 'success' ,
                confirmButtonColor: '#51cbce',
                confirmButtonText: 'OK'
              }).then(() => {
                self.updateBankModal = false;
              });
            } else {
              self.$swal({
                title: 'Error',
                text: data.errorMessage,
                icon: 'error',
                confirmButtonColor: '#ef8157',
                confirmButtonText: 'OK'
              });
            }
              console.log(data);
          }).catch(function() {

          }).then(function() {
            self.getPaginatedBanks(1, "");
          });
        }
      });
    },
  }
};
</script>